.sidebar.widget-area{
	border-left: 1px dashed #e0e0e0;
	padding-left: 0;
	@media screen and (max-width: 990px){ 
		padding-left: 15px; 
		border-left: none;
	}

	.widget{
		padding-left: 50px;
		padding-bottom: 30px;
		@media screen and (max-width: 990px){ padding-left: 15px; }
		&:not(:last-child){
			border-bottom: 1px dashed #e0e0e0;
		}

		@media screen and (max-width:990px){
			padding: 0 0 30px;
		}
	}
}