.woocommerce{
	@include clearfix;
}
//- Star Rating
.star-rating {
	display: inline-block;
	overflow    : hidden;
	position    : relative;
	height      : 1em;
	line-height : 1;
	font-size   : 1em;
	width       : 5.4em;
	font-family : themify;

	&:before {
		content  : "\e60a\e60a\e60a\e60a\e60a";
		color    : #d3ced2;
		float    : left;
		top      : 0;
		left     : 0;
		position : absolute;
		color    : $detail-color;
	}

	 span {
		overflow    : hidden;
		float       : left;
		top         : 0;
		left        : 0;
		position    : absolute;
		padding-top : 1.5em
	}

	 span:before {
		content  : "\e60a\e60a\e60a\e60a\e60a";
		top      : 0;
		position : absolute;
		left     : 0;
		color    : $accent-color-2;
	}
}

//- Star Rating
p.stars {
	position  : relative;
	font-size : 1em;

	a {
		display       : inline-block;
		font-weight   : 700;
		margin-right  : 1em;
		text-indent   : -9999px;
		position      : relative;
		border-bottom : 0!important;
		outline       : 0;

		&:last-child {
			border-right: 0
		}

		&.star-1,&.star-2,&.star-3,&.star-4,&.star-5 {
			border-right: 1px solid #ccc;

			&.active:after{
				color: $accent-color-2;
			}
		}

		&.star-1:after,&.star-2:after,&.star-3:after,&.star-4:after,&.star-5:after {
			font-family : "themify";
			text-indent : 0;
			position    : absolute;
			top         : 0;
			left        : 0;
			color       : $detail-color;
		}

		&.star-1 {
			width: 2em
		}

		&.star-1:after {
			content: "\e60a"
		}

		&.star-1.active:after,&.star-1:hover:after {
			content: "\e60a"
		}

		&.star-2 {
			width: 3em
		}

		&.star-2:after {
			content: "\e60a\e60a"
		}

		&.star-2.active:after,&.star-2:hover:after {
			content: "\e60a\e60a"
		}

		&.star-3 {
			width: 4em
		}

		&.star-3:after {
			content: "\e60a\e60a\e60a"
		}

		&.star-3.active:after,&.star-3:hover:after {
			content: "\e60a\e60a\e60a"
		}

		&.star-4 {
			width: 5em
		}

		&.star-4:after {
			content: "\e60a\e60a\e60a\e60a"
		}

		&.star-4.active:after,&.star-4:hover:after {
			content: "\e60a\e60a\e60a\e60a"
		}

		&.star-5 {
			width: 6em;
			border: 0
		}

		&.star-5:after {
			content: "\e60a\e60a\e60a\e60a\e60a"
		}

		&.star-5.active:after,&.star-5:hover:after {
			content: "\e60a\e60a\e60a\e60a\e60a"
		}
	}
}

//- Product Sorting
.product-sorting{
	display         : flex;
	justify-content : space-between;
	align-items     : center;
	padding: 30px 0;
	&.no-flexbox &{
		@include clearfix;
		margin-bottom: 50px;
	}

	.woocommerce-ordering{
		.no-flexbox &{
			float: right;
		}
		select{
			@media screen and (max-width: 480px){
				width: 200px;
			}
		}
	}
}

//- Product view control
.product-layout-view{
	.no-flexbox &{
		float: left;
	}
	a{
		display       : inline-block;
		padding       : 10px;
		font-size     : 24px;
		border        : 3px solid $border-color;
		line-height   : .5;
		margin-right  : 10px;
		border-radius : 10px;

		&:hover, &.active{
			color: $accent-color-2;
			border-color: inherit;
		}
	}
}

//- Woocommerce pagination
.woocommerce-pagination{
	padding: 50px 0;
	ul.page-numbers{
		list-style   : none;
		padding      : 0;
		display: block;
		li{
			display: inline-block;
		}

		.page-numbers{
			padding          : 13px 15px 10px;
			margin-right     : 10px;
			display          : block;
			background-color : white;
			line-height      : 1;
			box-shadow       : 0 2px 10px rgba(192, 221, 238, 0.7);
			font-weight      : 700;
			&.current{
				background-color: $accent-color-2;
				color: white;
			}
			&.dots{
				color: $detail-color;
			}
		}
	}
}

//- Review
.woocommerce-Reviews{
	@include clearfix;
	margin: 0 -15px;
	display: flex;

	> div{
		width: 50%;
		float: left;
		flex-grow: 1;
		padding: 0 15px;

		@media screen and (max-width: 990px){
			width: 100%;
			float: none;
		}
	}
	.clear{
		display: none
	}
	@media screen and (min-width: 991px){
		> div:first-child{
			padding-right: 30px;
			border-right: 1px dashed #e0e0e0;
			+ div{
				padding-left: 30px;
				
			}
		}
	}
	.woocommerce-Reviews-title,
	.comment-reply-title{
		font-size: 1.5em;
		margin: 0 0 30px;
	}

	.comment-respond{
		margin-top: 0;
	}

	.comment{
		min-height    : 80px;
		margin-bottom : 30px;
		&_container{
			padding-left: 80px;
			position: relative;
		}

		.avatar{
			position: absolute;
			left: 0;
			border-radius: 50%;
		}

		@media screen and (min-width:640px){
			.star-rating{
				position: absolute;
				right: 0;
				top: 0;
			}
		}
	}
	.comment-form-author,.comment-form-email{
		width: 50%;
	}
}

//- Woocommerce form & grid
.col2-set{
	width: 100%;
	clear: both;
	&:after{
			content: "";
			display: block;
			clear: both
	}
	.col-1{
			float: left;
			width: 50%;
			padding-right: 15px;
	}
	.col-2{
			float: right;
			width: 50%;
			padding-left: 15px;
	}
	.col-1, .col-2{
		@media screen and (max-width: 990px){
			width: 100%;
			padding: 0;
			float: none;
		}
	}
}

form {
	
	.form-row{
		padding: 3px;
		margin: 0 0 15px;

		&:after{
				content: "";
				display: block;
				clear: both
		}
		label{
			display: block;
			margin-bottom: 10px;
		}

		label.hidden{
				visibility: hidden
		}

		select{
				width: 100%;
				cursor: pointer;
				margin: 0
		}

		.required{
				color: red;
				font-weight: bold;
				border: 0
		}

		.input-checkbox{
				display: inline;
				margin: -2px 8px 0 0;
				text-align: center;
				vertical-align: middle
		}

		.inline{ 
			display: inline-block; 
			margin-left: 30px;
		}

		label.checkbox{
				display: inline
		}

		input.input-text,textarea{
				-webkit-box-sizing: border-box;
				-moz-box-sizing: border-box;
				box-sizing: border-box;
				width: 100%;
				margin: 0;
				outline: 0;
				line-height: 1em
		}

		textarea{
				height: 3.5em;
				line-height: 1.5em;
				display: block;
				-moz-box-shadow: none;
				-webkit-box-shadow: none;
				box-shadow: none
		}
		&.woocommerce-validated input.input-text{
				background-image: url(images/icons/valid.png);
				background-position: 95% center;
				background-repeat: no-repeat
		}

		&.woocommerce-invalid input.input-text{
				background-image: url(images/icons/invalid.png);
				background-position: 95% center;
				background-repeat: no-repeat
		}

		&.woocommerce-invalid .chzn-single,&.woocommerce-invalid .chzn-drop,&.woocommerce-invalid input.input-text,&.woocommerce-invalid select {
				border-color: #fb7f88
		}

		::-webkit-input-placeholder{
				line-height: 1em;
				padding-top: 2px
		}
		:-moz-placeholder{
				line-height: 1em;
				padding-top: 2px
		}

		:-ms-input-placeholder{
				line-height: 1em;
				padding-top: 2px
		}
	}

	.form-row-first,.form-row-last{
			float: left;
			width: 50%;
			overflow: visible;

		@media screen and (max-width: 758px){
			width: 100%;
			float: none;
			padding-left: 0!important;
		}
	}

	.form-row-last{
		float: right;
		padding-left: 30px;
	}

	.form-row-wide{
			clear: both
	}

	.select2-container .select2-choice{
		border-radius: 20px;
		padding: 5px 20px;
		background-color: transparent;
		border-width: 3px!important;
		border-color: #BDBDBD;
	}
	.select2-dropdown-open .select2-choice{
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
	}
}
.select2-drop-active{
	border: 3px solid #bdbdbd;
	border-top: none;
	padding-top: 10px;
}

//- variation list
dl.variation{
	@include clearfix;
	margin: 0;
	dt,dd{ 
		float: left;
		margin: 0;
	}
	dt{
		clear: both;
		margin-right: 10px;
		text-transform: capitalize;
	}
	p{
		margin: 0;
	}
}

// Shop-cart
.shop_table{
	&.cart{
		margin-bottom: 30px;
		thead{
			@media screen and (max-width: 768px){ display: none }
			th{
				padding: 30px 10px;
			}
		}
		tbody{
			th,td{
				padding: 15px 20px;
			}
		}

		.remove{
			width       : 16px;
			height      : 16px;
			overflow    : hidden;
			text-indent : -9999px;
			position    : relative;
			display     : inline-block;

			&:before{
				content     : "\e053";
				font-family : "dripicons-v2";
				font-size   : 16px;
				width       : 100%;
				position    : absolute;
				text-indent : 0;
				left        : 0;
				top         : 0;
				line-height : 1;
			}
		}

		.product-remove{
			padding: 10px;
			text-align: center;
			width: 50px;
			@media screen and (max-width: 768px){
				position: absolute;
				right: 10px;
				top: 10px;
			}
		}
		.product-thumbnail{
			text-align: center;
			width: 100px;
			img{
				width: 70px;
				height: auto;

				@media screen and (max-width: 768px){
					width: auto;
				}
			}
		}
		th.product-name{
			text-align: left
		}
		.product-name{
			text-align: left;
			a{
				font-weight: 700;
			}
		}
		.product-price{
			text-align: right;
		}
		.product-quantity{
			width: 100px;

			input{
				max-width: 100px;
			}
		}
		.product-subtotal{
			text-align: right;
			padding-right: 20px;
		}

		.actions{
			.coupon{
				float: left;

				@media screen and (max-width: 768px){
					float: none;
					margin-bottom: 10px;

					input{ 
						width: 100%;
						margin-bottom: 10px;
					}
				}
			}
			>.button{
				float: right;
				@media screen and (max-width: 768px){
					float: none;
					width: 100%;
				}
			}
		}

		.cart_item{
			position: relative;
			@media screen and (max-width: 768px){
				display: block;
				padding: 20px 15px;
			}
			td{
				@media screen and (max-width: 768px){
					display: block;
					padding: 5px 10px;
				}
			}

			[data-title]{
				@media screen and (max-width: 768px){
					text-align: left;
					width: 100%;
					&:before{
						content: attr(data-title);
						width: 100px;
						white-space: nowrap;
						font-weight: 700;
						display: inline-block;
						vertical-align: middle;
					}

					.quantity{
						display: inline-block;
					}
				}
			}
		}
	}

}
.cross-sells{
	clear: both;
}
.cart-collaterals{
	width: 50%;
	float: right;
	padding-left: 30px;
	margin-bottom: 100px;

	@media screen and (max-width: 768px){
		width: 100%;
		float: none;
		padding-left: 0;
	}

	.shop_table{
		margin-bottom: 30px;
		table-layout: fixed;
		td{
			padding: 20px;
			text-align: right
		}
	}
}
#shipping_method{
	list-style: none;
	text-align: left;
	margin: 0;
	padding: 0;

	input[type="radio"]{ 
		display: none;
		&:checked + label:after{
			transform: scale(1);
		} 
	}
	label{
		display: block;
		padding-left: 40px;
		position: relative;
		&:before, &:after{
			content:" ";
			position: absolute;
			left: 0;
			top: 0;
		}

		&:before{
			width: 20px;
			height: 20px;
			border: 2px solid $accent-color-1;
			border-radius: 50%;
			top: 3px;

			.woocommerce-checkout-review-order-table &{
				border-color: white;
			}
		}
		&:after{
			width: 12px;
			height: 12px;
			border-radius: 50%;
			background-color: $accent-color-1;
			top: 7px;
			left: 4px;
			transform: scale(0);
			transition: .3s ease;
			.woocommerce-checkout-review-order-table &{
				background-color: white;
			}
		}
	}
}

//- Woocommerce Messages
.woocommerce-info,
.woocommerce-message{
	background-color : #15a3f9;
	color            : #01263d;
	padding          : 30px 30px 30px 80px;
	position         : relative;
	box-shadow       : 0 10px 30px rgba(0,0,0,.1);
	margin-bottom    : 30px;
	@include clearfix;

	.button{
		float: right;
		padding: 3px 20px;
		margin: 0;
	}

	@media screen and (max-width:480px){
		padding: 15px;
		text-align: center;

		span{
			display: block;
			margin-bottom: 15px;
		}

		.button{
			color: white;
			padding: 5px 20px;
			float: none;
			display: inline-block;
		}
	}

	&:before{
		content:"\e61c";
		font-family: "themify";
		position: absolute;
		left: 30px;
		top: 30px;
		@include em(18);
		color: rgba(white,.5);

		@media screen and (max-width:480px){
			color: white;
			position: relative;
			left: 0;
			right: 0;
			top: 0;
			display: block;
			font-size: 32px;
			text-align: center;
			font-weight: 300;
		}
	}
}

//- Woocommerce Error
.woocommerce-error{
	background-color : #f6604c;
	color            : #01263d;
	list-style       : none;
	padding          : 30px 30px 30px 80px;
	position         : relative;
	box-shadow       : 0 10px 30px rgba(0,0,0,.1);
	margin-bottom    : 30px;

	&:before{
		content     :"\e646";
		font-family : 'themify';
		@include em(18);
		position    : absolute;
		left        : 30px;
		top         : 30px;
		color       : rgba(white,.5);
	}
}

//- Woocommerce Login
.woocommerce .login,
.woocommerce .register,
.woocommerce .checkout_coupon{
	background-color : white;
	box-shadow       : 0 10px 30px rgba(0,0,0,.1);
	padding          : 30px;
	margin-bottom    : 30px;
	@include clearfix;

	.section-header{
		margin-top: 0;
	}
}
.woocommerce-password-strength{
	@include em(14);
	margin: 10px 0;
}
.woocommerce-password-hint{
	padding: 10px;
	border-radius: 3px;
	border: 1px solid #e3e3e3;
	display: block;
}

//- Woocommerce Checkout
.woocommerce .woocommerce-checkout{
	margin: 0 -15px;
	.col2-set{
		width: 50%;
		float: left;
		padding: 0 15px;

		@media screen and (max-width: 990px){
			width: 100%;
			float: none;
		}

		.col-1,.col-2{
			width: 100%;
		}
		.col-1{ padding-right: 0 }
		.col-2{ padding-left: 0 }
	}

	> h3, .woocommerce-checkout-review-order{
		width: 50%;
		float: right;
		padding: 0 15px;

		@media screen and (max-width: 990px){
			width: 100%;
			float: none;
		}
	}
}

//- Woocommerce checkout review order
.woocommerce-checkout-review-order-table{
	th,td{
		padding: 20px;
	}
}

//- woocommerce checkout payment
.woocommerce-checkout-payment{
	background-color: white;
	box-shadow       : 0 10px 30px rgba(0,0,0,.1);

	.place-order{
		padding: 20px;
		text-align: right;
	}
}
.wc_payment_methods.payment_methods{
	list-style: none;
	padding: 0;
	margin: 0;

	li:not(.wc_payment_method){
		padding: 20px;
	}

	.wc_payment_method{
		input{
			display: none;
			&:checked + label:after{
				transform: scale(1);
				opacity: 1;
			}
		}
		label{
			display          : block;
			padding          : 20px 20px 20px 60px;
			background-color : #f1f4f6;
			color            : $heading-color;
			font-weight      : 700;
			position         : relative;
			@include clearfix;

			&:before{
				content:" ";
				width: 24px;
				height: 24px;
				border-radius: 50%;
				border: 2px solid $accent-color-1;
				left: 20px;
				top: 0;
				bottom: 0;
				margin: auto 0;
				position: absolute;

			}
			&:after{
				content          :" ";
				width            : 14px;
				height           : 14px;
				position         : absolute;
				left             : 25px;
				top              : 0;
				bottom           : 0;
				margin           : auto 0;
				background-color : $accent-color-1;
				border-radius    : 50%;
				transform: scale(0);
				opacity: 0;
				transition: .3s ease;
			}

			img{
				float: right;
				max-width: 100px;
			}

			a{
				font-style: italic;
				display: inline-block;
				margin-left: 10px;
				color: $detail-color;
				font-size: 80%;
			}
		}
		.payment_box{
			padding: 20px;
		}
	}



}

.woocommerce-MyAccount-navigation{
	ul{
		list-style: none;
		margin-left: 0;
		margin-bottom: 50px;
		padding: 0;
		border-bottom: 3px solid #eaeaea;
		
		@media screen and (max-width:640px){
			display: flex;
			flex-wrap: wrap;
		}
	}

	&-link{
		position: relative;
		display: inline-block;
		bottom: -2px;
		@media screen and (max-width:640px){
			flex-basis: 50%;
			display: flex;
		}
		@media screen and (max-width: 320px){
			flex-basis: 100%;
		}

		a{
			border-bottom: 2px solid transparent;
			display: block;
			padding: 15px 30px;
			color: inherit;
			flex:1;
			@include em(14);
			@media screen and (max-width:640px){
				padding: 10px;
				box-shadow: 0 0 1px rgba(0,0,0,.3);
				background-color: white;
			}

			&:before{
				font-family: 'themify';
				display: inline-block;
				margin-right: 10px;
			}

		}
		&--dashboard a:before{ content:"\e6aa" }
		&--orders a:before{ content:"\e687" }
		&--edit-address a:before{ content:"\e61c" }
		&--edit-account a:before{ content:"\e61d" }
		&--downloads a:before{ content:"\e6a7" }
		&--customer-logout a:before{ content:"\e732" }

		&.is-active a,
		&:hover a{
			color: $accent-color-1;
			
			border-color: inherit;
		}
	}
		
}

.woocommerce-MyAccount-orders{
	width: 100%;

	thead{
		border-bottom: 2px solid #666;
	}

	th{
		text-align: left;
	}

	tbody tr{
		border-bottom: 1px solid #e3e3e3;
	}
}

mark{
	display: inline-block;
	padding: 4px 10px;
	border-radius: 5px;
	background-color: #cdf2ff;
}

.woocommerce-EditAccountForm.edit-account{
	fieldset{
		border: 1px solid #e3e3e3;
		margin-bottom: 30px;
	}
}

.wcbs-fbt-section{
	margin-bottom: 50px;
	.wcbs-fbt-images{
		display          : inline-table;
		width            : auto;
		background-color : transparent;
		box-shadow       : none;
		float            : left;

		img{
			height: auto;
			width: 100px;
		}

		tbody{ background-color: transparent; }

		tr{
			background-color: transparent;
		}
	}
	.wcbs-fbt-submit-block{
		float: left;
		padding-left: 20px;

		.total_price_label{
			display: block;
			font-family: $font-secondary;
			@include em(18);
		}
		.total_price{
			color: $accent-color-1;
			@include em(18);
			font-weight: 700;
			margin-bottom: 10px;
			display: block;
		}
	}
	.wcbs-fbt-items{
		clear: both;
		list-style: none;
		padding-left: 0;
		font-weight: 700;

		li{
			
			&:before{
				content:"\53";
				font-family: "dripicons-v2";
				color: green;
				display: inline-block;
				margin-right: 10px;
			}
		}

		.current_item{
			font-style: italic;
			color: $detail-color;
		}
		.price{
			color: $accent-color-1;
			font-size: inherit;
			del{ 
				margin-right: 10px;
				display: inline-block;
			}
		}
	}
}

//- Yith Quickview
.yith-wcqv-main{
	box-shadow: 0 10px 30px -4px rgba(0,0,0,.3)!important;
	#yith-quick-view-close{
		width: 32px;
		height: 32px;
		border-radius: 50%;
		text-indent: -9999px;
		overflow: hidden;
		border: 3px solid $accent-color-1;
		color: inherit;

		&:before{
			content:"\39";
			font-family: "dripicons-v2";
			position: absolute;
			left: 0;
			width: 100%;
			text-indent: 0;
			line-height: 28px;
			font-size: 22px;
			display: block;
			text-align: center!important;
			padding: 0;

		}
	}
	.single-product{
		@include clearfix;
	}
	.product-overview{
		padding: 30px;
		position: relative;
	}
	.images, .summary{
		float: left;
	}
	.onsale{
		position: absolute;
		top: 10px;
		@include em(13);
		left: -10px;
		background-color: $accent-color-1;
		color: white;
		text-transform: uppercase;
		padding: 3px 10px;
		border-radius: 3px;
	}
	.images{
		width: 40%;
		padding-right: 30px;
		img{
			width: 100%;
			max-width: 100%;
			height: auto;
		}
	}
	.summary{
		width: 60%;
		padding-left: 40px;
	}
	.product_title{
		margin-top: 0;
	}
	.product_meta{
		padding-top: 10px;
	}
}

