/* ----- navigation ----- */
.vbox-close{
	cursor: pointer;
	position: fixed;
	top: -1px;
	right: 0;
	width: 46px;
	height: 40px;
	padding: 10px 20px 10px 0;
	display: block;
	background: url(../img/close.gif) no-repeat #161617;
	background-position:10px center;
	color: #fff;
	text-indent: -100px;
	overflow: hidden;
}
.vbox-next, .vbox-prev{
	box-sizing: content-box;
	cursor: pointer;
	position: fixed;
	top: 50%;
	color: #fff;
	width: 30px;
	height: 170px;
	margin-top: -85px;
	text-indent: -100px;
	border: solid transparent; /* Using border instead of padding to keep bg image in place */
	overflow: hidden;
}
.vbox-prev{
	left: 0;
	border-width: 0 30px 0 10px;
	background: url(../img/prev.gif) center center no-repeat;
}
.vbox-next{
	right: 0;
	border-width: 0 10px 0 30px;
	background: url(../img/next.gif) center center no-repeat;
}

.vbox-title{
	width: 100%;
	height: 40px;
	float: left;
	text-align: center;
	line-height: 28px;
	font-size: 12px;
	color: #fff;
	padding: 6px 40px;
	overflow: hidden;
	background: #161617;
	position: fixed;
	display: none;
	top: -1px;
	left: 0;

}
.vbox-num{
	cursor: pointer;
	position: fixed;
	top: -1px;
	left: 0;
	height: 40px;
	display: block;
	color: #fff;
	overflow: hidden;
	line-height: 28px;
	font-size: 12px;
	padding: 6px 10px;
	background: #161617;
	display: none;
}
/* ------- inline window ------ */
.vbox-inline{
	width: 420px;
	height: 315px;
	padding: 10px;
	background: #fff;
	text-align: left;
	margin: 0 auto;
	overflow: auto;
}

/* ------- Video & iFrames window ------ */
.venoframe{
	border: none;
	width: 960px;
	height: 720px;
}
@media (max-width: 992px) {
	.venoframe{
		width: 640px;
		height: 480px;
	}
}
@media (max-width: 767px) {
	.venoframe{
		width: 420px;
		height: 315px;
	}
}
@media (max-width: 460px) {
	.vbox-inline{
		width: 100%;
	}

	.venoframe{
		width: 100%;
		height: 260px;
	}
}

/* ------- PLease do NOT edit this! (or do it at your own risk) ------ */
.vbox-open{
	overflow: hidden;
}
.vbox-container{
	position: relative;
	background: #000;
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 15px;
}
.vbox-content{
	text-align: center;
	float: left;
	width: 100%;
	position: relative;
	overflow: hidden;
}
.vbox-container img{
	max-width: 100%;
	height: auto;
}