.equal-height{
	display: table;
	width: 100%;
	[class^="col-"]{
	float: none;
	display: table-cell;
	}

	[class^="col-sm-"]{
	@media screen and (max-width: $screen-sm){
		width: 100%;
		display: block;
	}
	}
	[class^="col-md-"]{
	@media screen and (max-width: $screen-md){
		width: 100%;
		display: block;
	}
	}
	[class^="col-lg-"]{
	@media screen and (max-width: $screen-lg){
		width: 100%;
		display: block;
	}
	}
}

.grid-layout{
	margin: 0 -15px;
	display: flex;
	flex-wrap: wrap;
	clear: both;

	@media screen and (max-width: 480px){
		margin: 0 -10px;
	}

	.no-flexbox &{
		@include clearfix;
	}

	&.v-align{ align-items: center }
	&.b-align{ align-items: flex-end }

	.grid-item{
		padding: 15px 15px;
		@media screen and (max-width: 480px){
			padding: 10px;
		}

		.no-flexbox &{
			float: left;
		}
	}

	&.columns-1 .grid-item{
		width: 100%;
	}
	&.columns-2 .grid-item{
		width: 50%;
		.no-flexbox &{
			&:nth-child(2n+1){
				clear: both;
			}
		}
		@media screen and (max-width: 320px){ width: 100% }
	}
	&.columns-3 .grid-item{
		width: 33.3333%;
		.no-flexbox &{
			&:nth-child(3n+1){
				clear: both;
			}
		}
		@media screen and (max-width: 640px){ 
			width: 50%;
			.no-flexbox &{
				&:nth-child(3n+1){ clear: none }
				&:nth-child(2n+1){ clear: both }
			}
		}
		@media screen and (max-width: 320px){ 
			width: 100%;
		}
	}
	&.columns-4 .grid-item{
