				clear: both;
			}
		}
		@media screen and (max-width: 1020px){ 
			width: 33.3333%;
			.no-flexbox &{
				&:nth-child(4n+1){ clear: none; }
				&:nth-child(3n+1){ clear: both; }
			}
		}
		@media screen and (max-width: 640px){ 
