.main-content{
	margin-bottom: 50px;
	@include clearfix;
	&.layout-left-sidebar{
		.content-area{
			float: right;
			padding-right: 15px;
			padding-left: 50px;

			@media screen and (max-width: 990px){ 
				padding-left: 15px;
				width: 100%;
			}
		}
		.sidebar.widget-area{
			padding-right : 0;
			padding-left  : 15px;
			border-left   : none;
			border-right  : 1px dashed #e0e0e0;

			@media screen and (max-width: 990px){ 
				padding-right: 15px;
				border-right: none;
				clear: both;
			}

			.widget{
				padding-right: 50px;
				@media screen and (max-width: 990px){ padding-right: 0 }
			}
		}
	}
}
.content-area.col-md-8{
	padding-right: 50px;
	@media screen and (max-width: 990px){ padding-right: 15px; }
}
