input:not([type="checkbox"]):not([type="radio"]),
select,
textarea{
	padding          : 8px 20px;
	border           : 3px solid $border-color;
	border-radius    : 50px;
	font-size        : inherit;
	font-family      : inherit;
	background-color : transparent;
	line-height      : normal;
	appearance       : none;
	display          : inline-block;

	&:focus{
		outline: none;
	}
}

textarea{
	min-height: 200px;
	border-radius: 20px;
}

select{
	appearance          : none;
	padding-right       : 60px;
	background-image    : url(../img/select-arrow.png);
	background-repeat   : no-repeat;
	background-position : right center;
	max-width           : 100%;
}

.button,input[type="submit"],input[type="reset"]{
	border           : 3px solid $accent-color-2;
	padding          : 10px 20px;
	border-radius    : 50px;
	color            : inherit;
	font-size        : inherit;
	display          : inline-block;
	background-color : transparent;
	transition       : .3s ease;
	line-height      : 1.5;
	text-decoration  : none;

	&:hover{
		border-color: $accent-color-1;
		color: $accent-color-2;
	}

	&--secondary{
		border-color: $heading-color;
		&:hover{
			border-color: $border-color;
			color: inherit;
		}
	}

	&--special{
		border: none;
		padding: 10px 50px 10px 0;
		overflow: hidden;
		position: relative;
		border-radius: 0;
		text-transform: uppercase;
		letter-spacing: 1px;

		&:after{
			content:" ";
			width: 100%;
			height: 2px;
			background-color: $accent-color-2;
			position: absolute;
			top: 0;
			bottom: 0;
			margin: auto;
			margin-left: 10px;

		}
		&-left{
			padding: 10px 0px 10px 50px;
			&:after{
				display: none;
			}
			&:before{
				content:" ";
				width: 100%;
				height: 2px;
				background-color: $accent-color-2;
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				margin-right: 10px;
			}
		}
	}
}


.form-group{
	margin-bottom: 30px;

	input,select{
		width: 100%;
	}
}