.dokan-dashboard{
	.page-header, .type-page .entry-content{ margin-bottom: 0; }
	.main-content{
		margin-bottom: 0;
	}
	.main-content > .container{
		width: 100%;
		max-width: 100%;
		padding: 0;
	}

	.dokan-dash-sidebar ul.dokan-dashboard-menu li.active,
	.dokan-dash-sidebar ul.dokan-dashboard-menu li:hover{
		background-color: $accent-color-1;
		background-image: linear-gradient(90deg, $accent-color-1,$accent-color-2);
		&:after{
			left: auto;
			right: 0;
		}
	}
	.dokan-dash-sidebar ul.dokan-dashboard-menu li.dokan-common-links a:hover{
		background-color: $accent-color-1;
	}
	

	.dokan-dash-sidebar{
		padding-top: 50px;
	}
	.dokan-dashboard-content{
		padding-top: 50px;
		padding-bottom: 50px;

		> article {
			@extend .container
		}
	}
	.dashboard-widget.sells-graph{
		.chart-container{
			overflow: hidden;

			table{
				width: auto;
			}
		}
	}
	ul.subsubsub{
		font-size: inherit;
		a{
			color: inherit;
		}
	}

	.dokan-table {
		.diviader{
			display: none;
		}
	}

}

.dokan-single-store{

	.profile-frame {

		.profile-info-box {
			.profile-info-img{
				display: block;
			}
		}
		.profile-info-summery{
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}
} 


.dokan-category-menu{
	border: none;

	h3.widget-title{
		padding: 0;
		background-color: transparent;
		border: none;
		margin-bottom: 20px;
	}
	#cat-drop-stack > ul li.parent-cat-wrap{
		background-color: transparent;
		ul{
			background-color: transparent!important;
		}
		a{
			margin: 0;
		}
	}
	#cat-drop-stack > ul li a{
		font-size: inherit;
	}
}
// .dokan-orders-content .dokan-orders-area ul.order-statuses-filter
.dokan-orders-content .dokan-orders-area ul.order-statuses-filter{
	padding-left: 0;
	color: inherit;
	font-size: inherit;
}
.dokan-orders-content .dokan-orders-area ul.order-statuses-filter li.active a{
	color: $accent-color-1;
}
.dokan-settings-content .dokan-settings-area .dokan-address-fields label{
	font-size: inherit;
}
.dokan-alert{
	p:first-child{ margin-top: 0;}
	p:last-child{ margin-bottom: 0;}
}

.dokan-dashboard-header{
	.entry-title{
		@include em(24);

		small{
			color: $accent-color-1;
		}
	}
}

.dokan-form-control{
	font-size: inherit;
}

input[type="submit"].dokan-btn-theme, a.dokan-btn-theme, .dokan-btn-theme{
	@extend .button;

	&:hover{
		background-color: transparent;
		color: inherit;
	}
}


.page-header .dokan-seller-search-form{
	margin: 30px auto 0;
	#search{
		width: 100%;
		border: none!important;
		padding: 20px 40px 20px 60px!important;
		border-radius: 40px!important;
		border: none!important;
		box-shadow: 0 1px 2px rgba(0,0,0,.1)!important;
		background-position: 30px center!important;
	}
}

textarea.dokan-form-control{
	border-radius: 20px;
	border: 3px solid #BDBDBD;
	padding: 10px 15px;
	background-color: transparent;
	color: inherit;
}

.vendor-details{
	list-style: none;
	margin: 0;
	padding: 0;

	.seller-rating{
		display: inline-block;
	}

	li{
		position: relative;
		padding: 10px 0 10px 150px;

		&:not(:last-child){
			border-bottom: 1px solid rgba(0,0,0,.05);
		}

		.label{
			position: absolute;
			left: 0;
			top: 10px;
			font-weight: 700;
			color: $heading-color;
		}
	}
}