.widget{
	&-top-products{
		ol{
			counter-reset: top-list;

			li{
				counter-increment: top-list;
				padding-left: 20px;
				position: relative;
				margin-bottom: 15px;
				&:before{
					content: counter(top-list) '.';
					position: absolute;
					left: 0;
					top: 0;
					font-weight: 700;
					font-style: italic;
					color: $heading-color;
					line-height: 1;
				}
			}
		}
		h4{ 
			font-weight: 400;
			margin: 0;
			line-height: 1;
		}
		.author{ 
			color: $detail-color;
			font-style: italic;
			font-family: $font-secondary;
		}
	}

	&.tokoo-contact-info{
		text-align: center;
		.contact-phone{
			@include em(24);
			color: $accent-color-1;
			display: block;
			text-align: center;
			line-height: 1;
		}
		.contact-email{
			display: block;
			text-align: center;
		}
	}
	&.social-network{
		text-align: center;
	}

	&.tokoo-mailchimp-subscribe-form{
		text-align: center;
		.mc4wp-form-fields{
			position: relative;
			label{
				display: none;
			}
			input[type="email"],
			input[type="text"]{
				width: 100%;
				padding-right: 100px;
			}
			input[type="submit"]{
				@extend .button--special;
				border: none;
				color: $accent-color-1;
				position: absolute;
				top: 0;
				bottom: 0;
				right: 0;
				padding-right: 20px;
			}
		}
	}
}

.widget_mc4wp_form_widget{
	label{
		display: none;
	}
}
.mc4wp-form-fields{
	position: relative;

	label{
		display: none;
	}
	input[type="text"],
	input[type="email"]{
		width: 100%;
		padding-right: 100px;
	}

	input[type="submit"]{
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		border: none;
	}
}

/* Login Widget */
.widget{
	&.tokoo{

		&-login-form{
			input:not([type="checkbox"]){ width: 100% }
			.remember-me{float: left}
			.login-submit{float: right}

			.forget-link{
				clear: both;
				padding-top: 20px;
				margin-bottom: 0;
				text-align: center;
				font-weight: 300;
			}
		}

		&-recent-comments-with-avatar{
			.post-list{
				li > a{
					float: left;
					margin-right: 15px;
				}
			}
		}

		&_themeforest_widget_ul{
			list-style: none;
			margin: 0 -5px 20px;
			@include clearfix;

			li{
				width: 33.3333%;
				float: left;
				padding: 0 5px;

				img{
					width: 100%;
					max-width: 100%;
					height: auto;
				}
			}

			.tokoo_read_more{
				text-align: center;
				font-style: italic;
			}
		}

		&-contact-info{
			[class*="contact"]{
				display: block;
			}
		}

		&-contact-info{
			text-align: center;
			p{
				// @include em(12);
			}
			.contact-phone{
				@include em(24);
			}
		}

		&-recent-tweets{
			.twitter-tweets-list{
				list-style-type: none;

				li{
					margin-bottom: 30px;
				}
			}
			.tweet{
				position: relative;
				@include clearfix;

				.tweet-avatar{
					float: left;
					margin-right: 20px;
				}
				.tweet-content{
					display: table;
				}
				.timespan{float: right}
			}
		}

		&-testimonials{
			blockquote{ margin: 0; }
		}


		&-themeforest-item{
			ul{
				list-style: none;
				margin: 0;
				@include clearfix;
			}

			li{
				width: 25%;
				float: left;

				img{
					display: block;
				}
			}

			.Tokoo_read_more{
				display: block;
				text-align: center;
				padding: 20px 0;
			}

		}
		&-post-type-list{
			.post-list{
				list-style: none;
				@include clearfix;
				margin: 10px 0;

				li{
					@include clearfix;
					margin: 0 0 20px;

					> a{
						float: left;
						margin-right: 10px;
					}
					.post-detail{
						display: table;
					}
				}

				.entry-title{
					margin: 0;
				}
				.date{
					display: block;
				}
			}
		}
		&-recent-comments-with-avatar{
			.post-list{
				list-style: none;
				@include clearfix;
				margin: 10px 0;

				li{
					@include clearfix;
					margin: 0 0 20px;

					> a{
						float: left;
						margin-right: 10px;
					}
					.post-detail{
						display: table;
					}
				}

				.entry-title{
					margin: 0;
					display: inline;
				}
				.date{
					display: block;
				}
			}
			
		}
	}

	.koo-photogrid{
		list-style: none;
		@include clearfix;

		li{
			float: left;
			width: 25%;
		}
		img{
			display: block;
			width: 100%;
			max-width: 100%;
			height: auto;
		}

	}

	.koo-image-slider{
		position: relative;
		.slides{
			list-style: none;
			margin: 0;
			@include clearfix;

			li{

			}

			img{
				width: 100%;
				max-width: 100%;
				height: auto;
			}
		}

		.flex-direction-nav{
			list-style: none;
			padding-left: 0;
			margin: 0;

			.flex-next,.flex-prev{
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				width: 32px;
				height: 32px;
				color: white;
				background-color: rgba(0,0,0,.4);
				text-align: center;
				line-height: 32px;
			}
			.flex-prev{ left: 0 }
			.flex-next{ right: 0 }
		}
	}

	.testimonials-list, .testimonial-slider .slides{
		list-style-type: none;
		margin-left: 0;
		li{
			margin-bottom: 30px;
		}

		blockquote {
			font-size: 24px;
			line-height: 1.5;
		}

		.testimonial-detail{
			padding-left: 70px;
			position: relative;
			min-height: 70px;

			figure{
				width: 50px;
				height: 50px;
				border-radius: 50%;
				overflow: hidden;
				position: absolute;
				left: 0;
				top: 0;
			}

			cite{
				display: block;
				font-style: normal;
			}
		}

	}

	&.tokoo-testimonials{
		.flex-control-nav{
			text-align: center;
			list-style: none;
			margin-left: 0;

			li{
				display: inline-block;
				margin: 0 4px;
			}

			a{
				display: block;
				width: 10px;
				height: 10px;
				background-color: rgba(#e3e3e3,.5);
				overflow: hidden;
				text-indent: -9999px;
				border-radius: 50%;

				&.flex-active{
					background-color: white;
				}
			}
		}
		
	}
}
.tokoo-advanced-categories{
	select{
		width: 100%;
	}
}
.tokoo_themeforest_widget_ul{
	list-style: none;
	@include clearfix;
	li{
		width: 25%;
		float: left;
		padding: 5px;

		img{
			display: block;
			width: 100%;
			height: auto;
		}
	}
}