.hero-carousel-wrap{
	position: relative;
	background-color : #efefef;
	.uil-ripple{
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		transition: opacity .3s ease;
	}
	
	.slick-initialized + .uil-ripple{
		opacity: none;
		z-index: -1;
	}
	&.style-1{
		.hero-carousel{
			white-space      : nowrap;
			overflow         : hidden;
			position         : relative;
			@include clearfix;

			[tabindex]:focus{ outline: none}
			
			&.slick-initialized .hero-item{
				opacity: 1;
			}
			.hero-item{
				padding     : 50px 25px;
				position    : relative;
				outline     : none!important;
				display     : inline-block;
				white-space : normal;
				opacity     : 0;
				transition  : .3s ease;

				&__inner{
					position: relative;
					box-shadow: 0 10px 20px -5px rgba(0,0,0,.6);
					@media screen and (max-width: 990px){
						box-shadow: none;
					}
				}

				.block-link{
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
					z-index: 1;
				}

				&__image{
					margin: 0;
					position: relative;

					&:after{
						content:" ";
						width: 100%;
						height: 100%;
						position: absolute;
						left: 0;
						top: 0;
						background: linear-gradient(transparent, rgba(0,0,0,.4));
					}
					@media screen and (max-width: 990px){
						box-shadow: 0 10px 20px -5px rgba(0,0,0,.6);
						margin-bottom: 30px;
					}
					img{
						width: 100%;
						display: block;
					}
				}
				&.slick-current{
					.hero-item__detail{
						opacity: 1;
						transform: translateY(0);
					}
				}
				&__detail{
					position : absolute;
					z-index    : 1;
					bottom     : 50px;
					left       : 50px;
					width      : 40%;
					opacity    : 0;
					transform: translateY(50px);
					transition : .5s ease;
					transition-delay: .3s;

					@media screen and (max-width: 1300px){
						width: 50%;
						bottom: 30px;
						left: 30px;
					}
					@media screen and (max-width: 990px){
						position: relative;
						bottom: 0;
						left: 0;
						right: 0!important;
						width: 100%;
						text-align: center!important;
						color: inherit!important;
					}

					&--right{
						left: auto;
						right: 50px;
						text-align: right;
						@media screen and (max-width: 1300px){
							right: 30px;
						}
					}
				}
				&__title{
					font-weight : 900;
					font-family : $font-secondary;
					@include em(48);
					line-height : 1;
					margin      : 0 0 10px;
					color       : white;
					@media screen and (max-width: 1300px){
						color: $heading-color;
						@include em(32);
					}
				}
				&__desc{
					margin: 0;
					color: rgba(white,.8);
					@media screen and (max-width: 990px){
						color: $detail-color;
					}
				}
			}
		}
	}

	&.style-2{
		.hero-carousel{
			.hero-item{
				position: relative;
				.hero-background, .bg{
					position: absolute;
					width: 100%;
					height: 100%;
				}
				.hero-background{
					overflow: hidden;

					.bg{
						background-size     : cover;
						background-position : center;

						&:before{
							content    :" ";
							width      : 100%;
							height     : 100%;
							position   : absolute;
							left       : 0;
							top        : 0;
							background : linear-gradient(transparent, rgba(0,0,0,.4));
						}
					}
				}

				.hero-content{
					position: relative;
					z-index: 1;

					.container{
						padding: 100px 40px;
						display: flex;
						align-items: center;
					}
				}

				&__inner{
					width: 100%;
					@include clearfix;

					
				}
				
				.block-link{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
					z-index: 1;
				}

				&__title{
					@include em(64);
					font-family : $font-secondary;
					color       : white;
					line-height : 1;
					margin      : 0;
					transition: .6s cubic-bezier(0.79, 0.03, 0.25, 1);
					transition-delay: .3s;
					opacity: 0;
					transform: translateY(100px);
				}

				&__desc{
					@include em(18);
					color       : rgba(white,.8);
					font-weight : 300;
					transition: .6s cubic-bezier(0.79, 0.03, 0.25, 1);
					transition-delay: .4s;
					opacity: 0;
					transform: translateY(100px);
				}

				&.slick-current{
					.hero-item__title,.hero-item__desc{
						transform: translateY(0);
						opacity: 1;
					}
				}

				&__detail{
					&--right{ 
						text-align: right;
						float: right;
						@media screen and (min-width: 991px){width: 50%;}
					}
					&--left{ 
						text-align: left;
						float: left;
						@media screen and (min-width: 991px){width: 50%;}
					}
					&--center{ text-align: center }
					&--light{
						.hero-background .bg:before{
							background: linear-gradient( transparent, rgba(white, .4) );
						}
						.hero-item__title{
							color: #333;
						}
						.hero-item__desc{
							color: rgba(0,0,0,.7);
						}
					}
				}
			}
		}

		.slick-prev,.slick-next{
			position    : absolute;
			z-index     : 2;
			@include em(36);
			border      : none;
			background  : none;
			color       : white;
			width       : 36px;
			height      : 36px;
			line-height : 1;
			top         : 0;
			bottom      : 0;
			margin      : auto;
		}
		.slick-prev{ left: 0 }
		.slick-next{ right: 0 }

		.slick-dots{
			list-style : none;
			position   : absolute;
			left       : 0;
			width      : 100%;
			bottom     : 0;
			margin     : 0;
			padding    : 20px 0;
			text-align : center;

			li{
				display: inline-block;
				padding: 0 5px;

				button{
					background    : white;
					text-indent   : -9999px;
					overflow      : hidden;
					width         : 8px;
					height        : 8px;
					padding       : 0;
					border-radius : 10px;
					border        : none;
					transition    : .3s ease;
				}

				&.slick-active button{
					width: 30px;
				}

			}
		}
	}
}