.tagcloud{
	a{
		font-size        : inherit!important;
		display          : inline-block;
		padding          : 5px 10px;
		background-color : $accent-color-2;
		color            : white;
		margin           : 0 10px 10px 0;
		line-height      : 1;
		position         : relative;

		&:before{
			content      :" ";
			width        : 0;
			border-width : 2px 5px;
			border-style : solid;
			border-color : rgba(0,0,0,.6) rgba(0,0,0,.6) transparent transparent;
			position     : absolute;
			left         : 0;
			top          : 100%;
		}
	}
}

.calendar_wrap{
	td,th{
		text-align: center
	}
}

.widget_rss{
	li{
		margin-bottom: 30px;
	}
	.rsswidget{
		display: block;
		font-weight: 600;
		color: $heading-color;
	}
	.rss-date{
		font-style: italic;
		font-family: $font-secondary;
		display: block;
		margin-bottom: 10px;
	}
	.rssSummary{
		@include em(14);
	}
	cite{
		display: block;
		text-align: right;
		@include em(14);
	}
}

.widget_archive{
	select{
		width: 100%;
	}
}

.widget_search{
	form{
		position: relative;
		input[type="text"],input[type="search"]{
			width: 100%;
			padding-right: 100px;
		}
		input[type="submit"]{
			position: absolute;
			right: 0;
			border: none;
			top: 0;
			bottom: 0;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			background-color: $accent-color-1;
			color: white;
		}
	}
}