.footer-feature-list{
	background-color: $secondary-color;
	color: white;
	padding: 30px 0;
	text-align: center;

	.feature__image{ 
		color: rgba(white,.6);
		@include em(36);
		display: inline-block;
		vertical-align: middle;
		margin-right: 20px;
		line-height: 1;

		[class*="ti"],[class*="ico"]{
			line-height: 1;
			&:before{ line-height: inherit; }
		}
	}
	.feature__title{
		color: white;
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		line-height: 1;
	}
}
.site-footer{
	border-top: 1px dashed #e0e0e0;

	a{
		color: $accent-color-1;
	}

	&__widget-area{
		padding: 70px 0;
		.grid-item{
			&:not(:last-child){
				border-right: 1px dashed #e0e0e0;
			}
			@media screen and (max-width: 768px){
				width: 100%!important;
				border-right: none!important;
			}
		}
	}

	.widget{
		.widget-title{margin-left: 0;}
	}

	&__colophon{
		background-color: white;
		padding: 20px 0;
		@media screen and (max-width: 480px){
			@include em(14);
		}
		.grid-item{
			@media screen and (max-width: 480px){
				width: 100%!important;
				text-align: center!important;
			}
		}
	}
}