//- Product Grid View
.product-grid{
	.product{
		margin-bottom: 50px;
		@media screen and (max-width: 480px){
			margin-bottom: 30px;
		}
		&:hover{
			.product__action{ opacity: 1 }
		}
		&__image{
			margin     : 0 0 20px;
			box-shadow : 0 5px 20px -3px rgba(0,0,0,.2);
			position: relative;
			transition: transform .3s ease;
			> a{ 
				overflow: hidden;
				display: block;
			}
			img{
				max-width : 100%;
				display   : block;
				width: 100%;
				height: auto;
				transition: .3s ease;
			}
		}
		&__action{
			position         : absolute;
			width            : 100%;
			height           : 100%;
			top              : 0;
			left             : 0;
			background-color : rgba(white,.9);
			display          : flex;
			flex-direction   : column;
			align-items      : center;
			justify-content  : center;
			opacity: 0;
			transition: .3s ease;

			.no-flexbox &{
				text-align: center;
				padding-top: 10%;
			}

			.button{
				border        : none;
				padding       : 10px;
				border-radius : 0;
				font-size     : 30px;
				line-height   : 1;
				color         : $accent-color-1;
				position      : relative;

				.dripicons-plus{
					font-size : 18px;
					position  : absolute;
					display   : block;
					top       : 0;
					right     : -10px;
					line-height: 1;
					transition: color .3s ease;
				}
				&.loading{
					.dripicons-plus{
						color: $accent-color-2;
						&:before{ 
							content:"\e011";
							display: block;
							position: relative;
							animation: rotating 2s linear infinite;
						}
					}
				}
				&.added{
					.dripicons-plus{
						color: #92dc34;
						&:before{ content:"\53" }
					}
				}
				.button-label{ display: none }
			}

			.yith-wcqv-button, .added_to_cart.wc-forward,.buy-now{
				text-transform : uppercase;
				letter-spacing : 1px;
				padding        : 5px 15px;
				font-weight    : 700;
				border-radius  : 40px;
				line-height    : 1;
				@include em(12);
				border: 3px solid;
			}
		}
		.onsale{
			position         : absolute;
			right            : -10px;
			top              : 20px;
			background-color : $accent-color-2;
			color            : white;
			text-transform   : uppercase;
			line-height      : 1;
			padding          : 5px 10px;
			border-radius    : 3px;
			z-index          : 2;
			@include em(14)
		}
		&__detail > .yith-wcqv-button{ display: none}
		&__title{
			@include em(18);
			margin      : 0;
			line-height : 1.3;
			
			@media screen and (max-width: 480px){
				@include em(16);
			}
			
			a{ 
				color           : inherit;
				text-decoration : none;
			}
		}
		&__meta{
			margin-bottom: 10px;
		}
		.author{
			font-family : $font-secondary;
			font-style  : italic;
			color       : $detail-color;
			@include em(14);
		}
		&__price{
			color: $accent-color-1;
			margin-bottom: 5px;
			@include em(16);

			@media screen and (max-width: 480px){
				@include em(14);
			}
		}
		&__rating{
			@include em(18);
			@media screen and (max-width: 480px){
				@include em(14);
			}
		}
	}

	+.view-all{
		margin-bottom: 100px;
		padding-top: 30px;

		.button{
			padding: 5px 40px;
		}
	}
}

//- Product List View
.product-list{
	.product{
		margin-bottom: 100px;
		@media screen and (max-width: 640px){
			clear: both;
		}
		&__inner{
			display: flex;
			justify-content: flex-start;

			@media screen and (max-width: 640px){
				display: block;
				@include clearfix;
			}
		}
		&__image{
			width: 200px;
			position: relative;
			margin: 0;
			flex-shrink: 0;

			@media screen and (max-width: 640px){
				width: 100%;
				margin-bottom: 30px;
			}

			img{
				height    : auto;
				max-width : 100%;
				width     : 100%;
				display   : block;
				box-shadow: 0 10px 20px -3px rgba(0,0,0,.2);
			}

			.onsale{
				position: absolute;
				right: -10px;
				top: 20px;
				background-color: $accent-color-2;
				color: white;
				text-transform: uppercase;
				line-height: 1;
				padding: 7px 10px;
				border-radius: 3px;
				z-index: 2;
				@include em(14)
			}
		}

		&__detail{
			padding-left: 40px;
			
			@media screen and (max-width: 640px){
				padding-left: 0;
			}

			> .yith-wcqv-button{ display: none}
			
		}

		&__meta{
			.author{
				font-family: $font-secondary;
				font-style: italic;
			}
		}

		&__title{
			margin: 0;
		}

		&__price{
			float: left;
			font-size: 18px;
			font-weight: 700;
			color: $accent-color-1;
		}
		&__action{
			float: right;

			.button, .added_to_cart.wc-forward{
				@include em(12);
				padding        : 5px 20px;
				text-transform : uppercase;
				font-weight    : 700;
				letter-spacing : 1px;
				position       : relative;
				line-height    : 1;

				.dripicons-plus{
					position  : relative;
					display   : inline-block;
					top       : -3px;
					line-height: .7;
					transition: color .3s ease;
				}

				&.loading{
					.dripicons-plus{
						color: $accent-color-2;
						&:before{ 
							content:"\e011";
							display: block;
							position: relative;
							animation: rotating 2s linear infinite;
						}
					}
				}
				&.added{
					.dripicons-plus{
						color: #92dc34;
						&:before{ content:"\53" }
					}
				}
			}
		}
	}
}

.added_to_cart.wc-forward{
	display: inline-block;
	color: $accent-color-1;
	border: 3px solid;
	border-radius: 40px;
}

@keyframes rotating{
	0%   { transform: rotate(0deg) }
	100% { transform: rotate(360deg) }
}

//- Single Product
.product-overview{
	padding : 100px 0;
	position: relative;
	@include clearfix;
	
	@media screen and (max-width: 480px){
		padding: 30px 0;
	}

	&__image{
		width: 40%;
		@media screen and (max-width: 768px ){
			margin-bottom: 40px;
		}

		.images{
			img{
				width: 100%;
				height:auto;
			}
			.thumbnails.columns-3{
				margin: 0 -5px;
				@include clearfix;
				a{
					width: 33.3333%;
					float: left;
					padding: 5px;
				}
			}
		}
	}

	&__summary{
		width: 60%;
		padding-left: 50px;
		position: relative;

		@media screen and (max-width: 768px ){
			padding-left: 0;
		}

		.onsale{
			position: absolute;
			right: 0;
			background-color: $accent-color-1;
			color: white;
			border-radius: 3px;
			padding: 2px 10px;
		}

		.product__title{
			@include em(48);
			font-family: $font-secondary;
			margin: 0 0 20px;
			line-height: 1.2;

			@media screen and (max-width: 480px){
				@include em(32);
			}
		}
		.product__meta{
			@include clearfix;
			.author{
				font-family: $font-secondary;
				font-style: italic;
				float: left;

				@media screen and (max-width: 480px){
					float: none;
				}
			}
			.woocommerce-product-rating{
				float: right;
				text-align: right;

				@media screen and (max-width: 480px){
					float: none;
					text-align: left;
					margin-top: 10px;
				}
				.star-rating{
					display: inline-block;
					text-align: left;
				}

				.woocommerce-review-link{
					color: $detail-color;
					font-size: 12px;
					display: block;
				}
			}

		}

		.product-bookmark{
			@include clearfix;

			.yith-wcwl-add-to-wishlist,.clear,.post__share{
				display: inline-block;
				vertical-align: middle;
				margin-top: 0;
			}
			.post__share{
				a{
					display       : inline-block;
					padding       : 5px 10px;
					border-radius : 3px;
					color         : white;
					line-height   : 1;
					margin-bottom : 3px;
					@include em(14);
					.fa{
						display        : inline-block;
						margin-right   : 5px;
						vertical-align : middle;
					}
					&.facebook    {
						background-color: $facebook;
						&:hover{ background-color: lighten($facebook, 10%); }
					}
					&.twitter     {
						background-color: $twitter;
						&:hover{ background-color: lighten($twitter, 10%); }
					}
					&.google-plus {
						background-color: $google-plus;
						&:hover{ background-color: lighten($google-plus, 10%); }
					}
					&.linkedin    {
						background-color: $linkedin;
						&:hover{ background-color: lighten($linkedin, 10%); }
					}
				}
			}
		}

		.product-deals-info{
			background-color: white;
			display: table;
			border: 1px solid #e0e0e0;
			box-shadow: 0 10px 40px rgba(0,0,0,.1);
			border-radius: 3px;
			margin-bottom: 20px;
			@include clearfix;
			
			.deal-countdown,.deal-saving{
				float: left;
			}

			.label{
				@include em(13);
				color: lighten($detail-color,5%);
			}

			.deal-countdown{
				padding: 10px 20px;
				border-right: 1px solid #e0e0e0;
				@media screen and (max-width: 1200px){
					width: 100%;
					border-right: none;
					border-bottom: 1px solid #e3e3e3;
					text-align: center;
				}
				.timer{
					font-weight: 700;
					@include em(18);
					display: inline-block;
					margin-right: 5px;
					color: $heading-color;
				}
				.count-num{
					display: inline-block;
					margin-right: 10px;
				}
			}
			.deal-saving{
				display: table;
				@media screen and (max-width: 1200px){
					width: 100%;
					table-layout: fixed;
				}
				.saving{
					display: table-cell;
					padding: 10px 20px;
					text-align: center;

					&:not(:last-child){
						border-right: 1px solid #e0e0e0;
					}

					> span{
						display: block;
						
						&.value{
							font-weight: 700;
							@include em(18);
							color: $heading-color;
						}
					}
				}
			}
		}

	}

	&__image,
	&__summary{
		float: left;

		@media screen and (max-width: 768px ){
			display : block;
			float   : none;
			width   : 100%;
		}
	}
	.product-action{
		.price{
			@include em(24);
			margin-bottom : 20px;
			color: $accent-color-1;
			font-weight: 700;
			del,ins{ display: inline-block; }
			del{
				color: $detail-color;
				margin-right: 10px;
				font-weight: 300;
			}
			ins{
				font-weight: 700;
			}
		}
		.WooZone-price-info{
			display: block;
			font-size: 14px;
			margin-top: 5px;
			color: #444;
			font-style: normal;
			font-weight: 400;
		}
	}
	.cart:not(.variations_form){
		@include clearfix;
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 30px;

		.quantity{
			display: flex;
		}

		.input-text.qty{ 
			margin-right: 20px; 
			.no-flexbox &{
				float: left;
				min-width: 150px;
				margin-right: 0;
			}
		}
		.button{
			margin: 0;
		}
	}
	.product_meta{
		padding-top: 30px;
		> span{
			display: inline-block;
			margin-right: 20px;
			color: $heading-color;

			* {
				color: $detail-color;
			}
		}
	}
}
.variations_form.cart{
	.price-wrap{
		color         : $accent-color-1;
		border-bottom : 1px dashed $border-color;
	}
	table.variations{
		background-color : transparent;
		box-shadow       : none;
		margin-bottom: 30px;

		tr,tbody{ background-color: transparent; }
		td,th{
			@media screen and (max-width:480px){
				display: block;
			}
		}
		.label{
			text-transform: capitalize;
		}
		.reset_variations{
			padding-left: 20px;
			color: $detail-color;
			&:before{
				content:"\5a";
				font-family: "dripicons-v2";
				display: inline-block;
				vertical-align: middle;
				margin-right: 5px;
				color: $accent-color-1;
			}
		}
	}
	.single_variation_wrap{
		.woocommerce-variation.single_variation{
			@include clearfix;
			.woocommerce-variation-description{ width: 100% }
			.woocommerce-variation-price{ 
				float   : left;
				padding : 10px 0;
			}
			.woocommerce-variation-availability{ 
				float   : right;
				padding : 10px;
				.out-of-stock{
					color: red;
					font-style: italic;
				}
				p{
					margin: 0;
				}
			}
		}
		.woocommerce-variation-add-to-cart{
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 30px;

			.no-flex &{
				display: block;
				@include clearfix;
			}

			.quantity{
				display: flex;
				margin-right: 20px;
				.no-flex &{
					float: left;
					margin-right: 0;

					input{
						min-width: 150px;
					}
				}
			}

			button[type="submit"]{
				margin: 0;
				.no-flex &{
					float: left;
				}
			}
		}
	}
}

.book-images{
	perspective     : 2000px;

	.book{
		width           : 300px;
		max-width       : 100%;
		position        : relative;
		transform-style : preserve-3d;
		transform       : rotateY(0);
		margin          : 0 auto;
		transition      : transform .6s ease;

		&.back-view{
			transform: rotateY(-180deg);
			.rtl &{
				transform: rotateY(180deg);
			}
		}

		&:not(.back-view):hover{
			transform       : rotateY(-30deg);
			.rtl &{ transform: rotateY(30deg) }

			.book__page--front{
				transform: rotateY(-20deg);
				.rtl &{ transform: rotateY(20deg) }
			}
			.book__page--first-page{
				transform: rotateY(-10deg);
				.rtl &{ transform: rotateY(10deg) }
			}

		}
		img{
			display: block;
			height: auto;
		}

		.placeholder{
			visibility: hidden;
			display: block;
			height: auto;
		}
		&__page{
			position         : absolute;
			left             : 0;
			top              : 0;
			transform-origin : 0;
			will-change      : transform;
			background-image : linear-gradient(45deg, white, #fafafa);
			transition       : transform .3s ease;

			img{
				display: block;
				height: auto;
			}

