.movie-images{
	text-align: center;

	.see-gallery{
		border: 2px solid $accent-color-1;
		border-radius: 40px;
		padding: 3px 20px;
		display: inline-block;
		transition: .3s ease;
	}
}
.movie-case{
	width: 300px;
	margin: 0 auto 30px;
	position: relative;
	transform-style: preserve-3d;
	perspective: 1000px;
	pointer-events: none;
	max-width: 100%;

	@media screen and (max-width: 480px){
		overflow: hidden;
	}

	&.is-open{
		@media screen and (min-width: 481px){
			.front-cover{
				transform: rotateY(-45deg);
				.rtl &{ transform: rotateY(45deg) }
			}
			.disc{
				transform: translateX(100px) rotate(15deg);

				.rtl &{ transform: translateX(-100px) rotate(-15deg) }
			}
			.back-cover{
				box-shadow: 0 10px 40px rgba(0,0,0,.4);
			}
		}

	}

	.cover-placeholder{
		visibility: hidden;
		display: block;
	}
	.front-cover, .back-cover{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		transform-style: preserve-3d;
	}

	.front-cover{
		transform-origin: 0;
		transition: .4s cubic-bezier(0.71, 0.02, 0.25, 1);;
		z-index: 3;

		.rtl &{
			transform-origin: 100% 0;
		}

		&:before{
			content:" ";
			width: 15px;
			height: 100%;
			position: absolute;
			background-color: #444;
			background-image: linear-gradient(90deg, #333, #444,#444 50%, #111, 51%, #111 52%,#333 53%, #333);
			left: 100%;
			transform-origin: 0;
			transform: rotateY(90deg);

			.rtl &{
				left: 0;
			}

			.no-preserve3d &{
				display: none;
			}
		}
		
	}

	.back-cover{
		background-color: #222;
		background-image: linear-gradient(45deg, #111, #555);
		border: 2px solid #222;
		border-radius: 3px;
		transition: .3s ease;
		box-shadow: 0 10px 30px rgba(0,0,0,.1);
		transform: translateZ(-4px);
		.no-preserve3d &{
			display: none;
		}
	}

	.disc{
		width: 280px;
		height: 280px;
		top: 0;
		bottom: 0;
		margin: auto;
		z-index: 2;
		border-radius: 50%;
		background-color: white;
		position: absolute;
		overflow: hidden;
		box-shadow: 0 2px 10px rgba(0,0,0,.3);
		border: 4px solid #aaa;
		transition: .3s cubic-bezier(0.71, 0.02, 0.25, 1);
		transform: rotate(-45deg);
		@media screen and (max-width: 480px){
			width: 200px;
			height: 200px;
		}
		.no-preserve3d &{
			display: none;
		}
		
		&:before{
			content:" ";
			width: 50px;
			height: 50px;
			border: 4px solid #aaa;
			background-color: #222;
			border-radius: 50%;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			border-radius: 50%;
			margin: auto;
		}
	}
	

	img{
		display: block;
		height: auto;
		margin: 0 auto;
	}

}

.music-cover-wrap{
	text-align: center;

	.music-cover{
		width: 450px;
		max-width: 100%;
		position: relative;
		margin: 0 auto 30px;

		.cover-placeholder{
			visibility: hidden;
			height: auto;
			display: block;
			width: 300px;
			max-width: 100%;
		}

		&.is-playing{
			.disc{
				animation: rotatingDisc 3s linear infinite;
			}
			.play{
				i:before{
					content:"\e019";
				}
			}
		}

		.front-cover,.disc{
			position: absolute;
			left: 0;
			top: 0;

		}
		.front-cover{
			width: 300px;
			max-width: 100%;
			z-index: 1;
			box-shadow: 5px 5px 20px rgba(0,0,0,.3);

			.rtl &{
				left: auto;
				right: 0;
			}

			img{
				width: 100%;
				height: auto;
				display: block;
			}
		}
		.disc{
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			display: block;
			left: auto;
			height: 95%;

			.rtl &{
				left: 0;
				right: auto;
			}
		}
		.play{
			position: absolute;
			width: 64px;
			height: 64px;
			text-align: center;
			padding: 0;
			font-size: 38px;
			line-height: .8;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			background-color: white;
			background-image: linear-gradient(15deg,#efefef,white);
			border-radius: 50%;
			border: 2px solid white;
			color: #444;
			box-shadow: 0 2px 10px rgba(0,0,0,.3), 0 0 70px 20px rgba(0,0,0,.3);
		}
	}

	
}

.product-overview{
	.product-overview__summary .wp-playlist{
		margin-top: 30px;
	}
	.wp-playlist{
		text-align: left;
		background-color: transparent;
		border: none;
		padding: 0;

		&-current-item{
			display: none;
		}
		.mejs-container{
			width: 100%;
		}
		&-item{
			padding: 10px;
			transition: .3s ease;
			overflow: hidden;

			&:before{
				content:"\e01a";
				font-family: "dripicons-v2";
				font-size: 24px;
				display: block;
				width: 36px;
				height: 36px;
				color: white;
				text-align: center;
				position: absolute;
				left: 10px;
				border-radius: 50%;
				background-color: $accent-color-1;
				background-image: linear-gradient(25deg, $accent-color-1, $accent-color-2);
				transform: translateX(-50px) rotate(-270deg);
				transition: .3s ease-in;
			}

			&.wp-playlist-playing,&:hover{
				padding-left: 70px;
				background-color: transparent;

				&:before{
					transform: translateX(0) rotate(0deg);
				}

			}
			.wp-playlist-caption{
				color: transparent;
				text-indent: -999px;
				line-height: 0;
			}
			&-title,&-artist{
				text-indent: 0;
				line-height: normal;
			}
			&-title{
				font-size: 18px;
				color: #333;
				display: block;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			&-artist{
				display: block;
				color: $detail-color;
			}
			&-length{
				top: 10px;
				right: 10px;
			}
		}

	}
}


@keyframes rotate3d{
	0%{
		transform: rotateY(0deg);
	}
	50%{
		transform: rotateY(-30deg);	
	}
	70%{
		transform: rotateY(360deg);
	}
	100%{
		transform: rotateY(0);
	}
}

@keyframes rotatingDisc{
	from{
		transform: rotate(0deg);
	}
	to{
		transform: rotate(360deg);
	}

}

.product__detail-nav{
	background-color : white;
	text-align       : center;
	ul{
		list-style: none;
		margin: 0;
		padding-left: 0;
	}
	li{
		display: inline-block;
		&.active a,
		&:hover a{
			color: $accent-color-1;
			border-color: $accent-color-1;
		}
	}
	a{
		display: block;
		padding: 20px 40px;
		border-bottom: 2px solid transparent;
	}
}
.product__detail-content{
	padding: 50px 0 100px;
	@include clearfix;
	.woocommerce-Tabs-panel{
		display: none;

		.shop_attributes{
			box-shadow: none;
			margin: 0 auto;
			@media screen and (min-width: 991px){
				width: 60%;
			}

			tbody{
				background: transparent;

				tr{
					background: none!important;
					border-bottom: 1px solid #e3e3e3;
				}

				th,td{
					text-align: left;
				}
			}
		}
	}
}

.product.product-category{
	a{
		display: block;
		position: relative;
	}

	img{
		width: 100%;
		height: auto;
	}

	h3{
		position    : absolute;
		width       : 100%;
		top         : 50%;
		transform   : translateY(-50%);
		text-align  : center;
		font-weight : 400;
		margin      : 0;
		@include em(18);
	}
	.count{
		background    : $accent-color-1;
		color         : white;
		font-size     : 12px;
		border-radius : 40px;
		position      : relative;
		top           : -10px;
		padding       : 2px 10px;
		font-weight   : 700;
	}
}

.single-book-author{
	padding: 100px 0;
}
.author-area{
	text-align: center;

	&.is_stuck{
		@media screen and (max-width: 990px){
			position: static!important;
			width: auto!important;

			+ div{
				display: none!important;

			}
		}
	}
	.author-bio{
		padding: 30px;
	}

	.author-bio__image{
		width: 150px;
		height: 150px;
		margin: 0 auto 30px;
		border-radius: 50%;
		overflow: hidden;
		background-color: white;
		box-shadow: 0 0 0 10px rgba(white,.3);

		img{
			width: 150px;
			height: 150px;
			object-fit: cover;
			display: block;
		}
	}
	.author-name{
		font-family: $font-secondary;
		@include em(36);
		margin-top: 0;
		margin-bottom: 5px;
	}
	.author-birthday{
		font-style: italic;
		color: $detail-color;
		display: block;
		margin-bottom: 30px;
	}

	.author-socials{
		a{
			padding: 10px;
			display: inline-block;
		}
	}

}




.browse-author-by-alphabet.filterable-nav {
	text-align : center;
	padding    : 50px 0;

	a {
		display       : inline-block;
		padding       : 3px 10px;
		color         : $detail-color;
		border-radius : 3px;
		line-height   : 1;

		&.current{
			color: white;
			background: linear-gradient(40deg,$accent-color-1,$accent-color-2);
