		position: fixed;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,.5);
		z-index: 1!important;
	}

	.look-inside-box{
		background-color : white;
		position         : fixed;
		left             : 50px;
		top              : 50px;
		right            : 50px;
		bottom           : 50px;
		margin           : auto;
		position         : absolute;
		padding-top      : 50px;
		border-radius    : 3px;
