			}
			@media screen and (min-width:1201px){
				.book-preview-pages{ right: 300px }
			}
		}
		
		.look-inside-book-detail, .look-inside-book-related{
			width    : 300px;
			position : absolute;
			top      : 50px;
			bottom   : 0;
			z-index  : 3;
			transition: .3s ease;
			background-color: white;
			@media screen and (max-width: 640px){
				display: none;
			}
			&:before{
				content:" ";
				width: 5px;
				position: absolute;
				height: 100%;
				background-color: #f0f0f0;
			}
			.tab-content{
				position: absolute;
				top: 0;
				bottom: 0;
				width: 100%;
				overflow: auto;
			}
			.section__title{
				text-transform: uppercase;
				margin: 0 0 10px;
				font-weight: 700;
				letter-spacing: 1px;
				padding: 10px 20px;
				border-bottom: 1px solid #e3e3e3;
				@include em(16);
			}
		}
		.toggle-detail-tab,
		.toggle-related-tab{
			width: 30px;
			height: 40px;
			line-height: 30px;
			font-size: 18px;
			padding: 5px 0;
			position: absolute;
			background-color: #f0f0f0;
			border: 1px solid #f0f0f0;
			color: #888;
			i{
				transform: rotate(180deg);
				display: inline-block;
				line-height: 1;
				position: relative;
				transition: .3s ease;
			}
		}
		.look-inside-book-related{
			padding: 0;
			right: 0;
			transform: translateX(100%);

			&:before{
				right: 100%;
			}
			
			.toggle-related-tab{
				right: 100%;
			}

			.product_list_widget{
				border-bottom: 1px solid #e3e3e3;
				padding: 10px 20px;
				margin: 0;
				img{
					width: 50px;
				}
				a{
					font-weight: 700;
				}
				.woocommerce-Price-amount.amount{
					@include em(14);
					margin-top: 5px;
				}
			}
		}
		.look-inside-book-detail{
			left: 0;
			transform: translateX(-100%);
			.tab-content{
				@media screen and (min-height:801px){
					display: flex;
					flex-direction: column;
					flex-wrap: wrap;
				}
			}
			&:before{
				left: 100%;
			}
			.toggle-detail-tab{
				left: 100%;
			}
			.book-item{
				display: flex;
				align-items: center;
				border-bottom: 1px solid #e3e3e3;
				padding: 20px;
				.book-image{
					width: 80px;

					img{
						border: 1px solid rgba(0,0,0,.1);
						height: auto;
						display: block;
					}
				}
				.book-detail{
					padding-left: 20px;
					h2{
						margin: 0;
						@include em(16);
					}
					>span{
						color: $detail-color;
						font-family: $font-secondary;
						font-style: italic;
						@include em(14);
					}
					.woocommerce-review-link{
						@include em(12);
					}
				}
			}
			.book-meta{
				padding: 20px;
				border-bottom: 1px solid #e3e3e3;
			}
			.shop_attributes{
				background-color: transparent;
				box-shadow: none;
				@include em(13);
				th,td,tr{
					background-color: transparent;
					text-align: left;
					p{
						margin: 0;
					}
				}
			
				th{
					color: $heading-color;
				}
				td{
				}
			}
			.book-review{
				flex: 1;
				overflow: auto;
				padding: 20px;

				.comment{
					border: 1px solid #e3e3e3;
					padding: 10px 10px 30px;
					position: relative;
					margin-bottom: 50px;
					text-align: center;
				}

				.meta{
					margin: 0;
				}
				.description{
					p{
						margin-top: 0;
					}
				}

				.avatar{
					width: 48px;
					height: 48px;
					display: block;
					margin: 0 auto;
					border-radius: 50%;
					margin-top: -30px;
					text-align: center;
					margin-bottom: 0;
					border: 5px solid white;
				}
				.star-rating{
					position: absolute;
					bottom: 15px;
					left: 0;
					right: 0;
					margin: auto;
					text-align: left;
				}
			}
		}
		.book-preview-pages{
			background-color: #fafafa;
			position: absolute;
			top: 50px;
			bottom: 0;
			left: 0;
			right: 0;
			z-index: 1;
			border-left: 1px solid #e3e3e3;
			border-right: 1px solid #e3e3e3;
			transition: .3s ease;

			@media screen and (max-width: 640px){
				left: 0!important;
				left: 0!important;
			}

			&:before, &:after{
				content:" ";
				width: 100%;
				height: 60px;
				position: absolute;
				left: 0;
				z-index: 1;
			}
			&:before{
				top: 0;
				background-image: linear-gradient(#fafafa,rgba(255,255,255,0));
			}
			&:after{
				bottom: 0;
				background-image: linear-gradient(rgba(255,255,255,0),#fafafa);
			}

			.book-preview-tools{
				position: absolute;
				bottom: 20px;
				right: 20px;
				z-index: 3;

				button{
					width: 48px;
					height: 48px;
					border:none;
					color: #666;
					background-color: transparent;
					font-size: 24px;
					margin: 0 5px;
					padding-top: 5px;
					background-color: white;
					background: linear-gradient(white, #fefefe);
					border-radius: 50%;
					box-shadow: 0 3px 10px rgba(0,0,0,.1);

					&:active{
						box-shadow: 0 0 3px lighten(blue,20%);
					}
				}

			}

			img{
				display: block;
				text-align: center;
				margin: 20px auto;
				border: 1px solid rgba(0,0,0,.3);
				box-shadow: 0 10px 20px -5px rgba(0,0,0,.3);
				max-width: 100%;
			}
			.book-pages-wrap{
				overflow: auto;
				max-height: 100%;
				padding: 50px 20px;
				position: absolute;
				left: 0;
				width: 100%;
				top: 0;
				height: 100%;
			}
			.book-pages{
				width: 600px;
				max-width: 100%;
				margin: 0 auto;
				transition: .3s ease;

				img{
					width: 100%;
					height: auto;
				}
			}
		}
	}
}

//- Deals related styles
.deal-end-soon{
	padding: 50px 0;
	@include clearfix;
	.product-overview{

		&__image{
			img{
				display: block;
				margin: 0 auto;
			}
		}
	}

	.price{
		margin: 20px 0;
		.sale-price{
			@include em(36);
			color: $accent-color-1;
			font-weight: 700;

		}
		.regular-price{
			@include em(14);
			del{
				text-decoration-color: $accent-color-1;
			}
			.amount{
				color: $accent-color-1;
			}
			color: $detail-color;
		}
	}

	.product-deals-info{
		background-color: white;
		display: table;
		border: 1px solid #e0e0e0;
		box-shadow: 0 10px 40px rgba(0,0,0,.1);
		border-radius: 3px;
		margin-bottom: 20px;
		@include clearfix;
		
		.deal-countdown,.deal-saving{
			float: left;
		}

		.label{
			@include em(13);
			color: lighten($detail-color,5%);
		}

		.deal-countdown{
			padding: 10px 20px;
			border-right: 1px solid #e0e0e0;
			@media screen and (max-width: 1200px){
				width: 100%;
				border-right: none;
				border-bottom: 1px solid #e3e3e3;
				text-align: center;
			}
			.timer{
				font-weight: 700;
				@include em(18);
				display: inline-block;
				margin-right: 5px;
				color: $heading-color;
			}
			.count-num{
				display: inline-block;
				margin-right: 10px;
			}
		}
		.deal-saving{
			display: table;
			@media screen and (max-width: 1200px){
				width: 100%;
				table-layout: fixed;
			}
			.saving{
				display: table-cell;
				padding: 10px 20px;
				text-align: center;

				&:not(:last-child){
					border-right: 1px solid #e0e0e0;
				}

				> span{
					display: block;
					
					&.value{
						font-weight: 700;
						@include em(18);
						color: $heading-color;
					}
				}
			}
		}
	}
}

.deal-countdown{
	padding-top: 10px;
	.timer{
		font-weight: 700;
		@include em(16);
		display: inline-block;
		margin-right: 2px;
		color: $heading-color;
		display: inline-block;
	}
	.label{
		@include em(14);
		text-transform: capitalize;
	}
	.count-num{
		display: inline-block;
		margin-right: 3px;
	}
}


.deal-tab-grid{
	.deal-tab-nav{
		list-style: none;
		text-align: center;
		padding-left: 0;
		margin: 50px 0;

		li{
			display: inline-block;

			&.active{
				a{
					border-color: $accent-color-1;
					color: $accent-color-1
				}
			}
		}


		a{
			border-radius: 40px;
			display: block;
			padding: 10px 20px;
			border: 3px solid transparent;
			transition: .3s ease;

		}
	}
	.deal-tab-content{
		@include clearfix;
	}
	.deal-tab-pane{
		display: none;

		&.active{
			display: block;
		}
	}
	.all-deals{
		padding: 50px 0;
		text-align: center;
	}
}

.intrinsic-ratio{
	position: relative;
	display: block;
	background-color: #c3c3c3;
	img{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		transition: .5s ease-out;

		&.lazy-loaded{
			opacity: 1;
		}
	}
}

// Update Woocommerce 3.0
.woocommerce-product-gallery {
	position: relative;
	padding-right: 0;

	.woocommerce-product-gallery__wrapper{
		max-width: none;
		@include clearfix;
		a, img{
			display: block;
		}

		img{
			height: auto;
		}
	}

	.woocommerce-product-gallery__trigger {
		position: absolute;
		top: 3px;
		right: 15px;
		display: block;
		height: 2em;
		width: 2em;
		border-radius: 3px;
		z-index: 99;
		text-align: center;

		img {
			display: none!important;
		}

		&:before {
		    font: normal normal normal 1em/1 FontAwesome;
		    font-size: inherit;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -moz-osx-font-smoothing: grayscale;
		    display: block;
		    content: "\f00e";
		    line-height: 2;
		    text-indent: 0;
		}
	}

	
	.flex-control-thumbs{
		padding: 0;
		margin-top: 15px;
		margin-left: -15px;
		margin-right: -15px;
		@include clearfix;

		li {
			list-style: none;
		    cursor: pointer;
		    float: left;
		    padding: 15px;

		    img {
		    	opacity: .5;
		    	display: block;
		    	max-width: 100%;
			    -webkit-transition: all,ease,.2s;
			    -moz-transition: all,ease,.2s;
			    transition: all,ease,.2s
		    }

		    .flex-active, &:hover img{
		    	opacity: 1;
		    }
		}

		&:after, &:before {
			content: "";
			display: table;
		}
	}

	&.woocommerce-product-gallery--columns-2 {

		.flex-control-thumbs {

			li{
				width: 50%;
			}

			li:nth-child(2n+1) {
			    clear: both
			}
		}
	}

	&.woocommerce-product-gallery--columns-3 {

		.flex-control-thumbs {

			li {
			    width: 33.3333%;
			}

			li:nth-child(3n+1) {
			    clear: both
			} 
		} 
	}

	&.woocommerce-product-gallery--columns-4 {

		.flex-control-thumbs {

			li {
			    width: 25%;
			}

			li:nth-child(4n+1) {
			    clear: both
			} 
		} 
	}
}