.site-header{
	background-color : white;
	position         : relative;
	z-index          : 99;
	max-width: 100%;

	.site-header-wrap.is-sticky &{
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;

		@media screen and (max-width: 990px){
			position: relative;
			top: 0;
		}

		.admin-bar &{
			top: 32px;

			@media screen and (max-width: 990px){
				position: relative;
				top: 0;
			}
		}
	}

	&.sticky-enabled{
		position : fixed;
		top      : 0;
		left     : 0;
		width    : 100%;

		@media screen and (max-width: 990px){
			position: relative;
		}
	}

	.container, .container-fluid{
		display   : flex;
		flex-wrap : wrap;

		.no-flexbox &{
			display: table;
			width: 100%;
		}
	}

	&--type-2{
		.hdr-widget--product-search{
			// display: none;
		}
		.menu-main-toggle{
			 @media screen and (min-width: 991px){
				 display: none;
			 }
		 }
	}
}

.hdr-widget{
	display: flex;
	align-items: center;
	padding: 30px 20px;
	position: relative;

	.no-flexbox &{
		display: table-cell;
		vertical-align: middle;
	}

	&:not(:last-child){
		&:after{
			content:" ";
			width: 1px;
			height: 30px;
			position: absolute;
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			background-color: rgba(0,0,0,.1);
		}
	}


	//- Main menu
	&--menu-main{
		position: relative;
		padding: 0;
		&:after{
			display: none;
		}

		&.open-onhover:hover{

			.menu-main-wrapper{
				pointer-events: auto;
				z-index: 1;
				transform: translateY(0);
				opacity: 1;
			}
		}

		.menu-main-toggle{
			padding-left: 0;
		}
	}

	 &-dropdown-menu{
		 flex: 1;
		 align-items: stretch;

		 .menu-main-dropdown{
			 display: flex;
			 align-items: stretch;
			 padding-left: 15px;
		 }

		 .menu{
			 list-style: none;
			 padding: 0;
			 margin: 0;
			 display: flex;
			 @include clearfix;
			 @media screen and (max-width: 990px){ display: none }

			 &-item{
				//  float: left;
				display: flex;
				position: relative;

				 a{
					 padding: 10px 15px;
					 display: block;
					 display: flex;
					 align-items: center;

					 i{
						 margin-right: 10px;
						 display: inline-block;
					 }
				 }

				 &:hover {
					> a{
						color: $accent-color-1;
					}
					> .sub-menu{
						pointer-events: auto;
						opacity: 1;
						transform: translateY(0);
						visibility: visible;
						
						.no-csspointerevents &{
							display: block;
						}
					}
				 }
			 }

			 > .menu-item{
				> a{
					&:before{
						content:" ";
						width: 0;
						height: 5px;
						background-color: $accent-color-1;
						background-image: linear-gradient(90deg, $accent-color-1, $accent-color-2);
						position: absolute;
						left: 0;
						bottom: 0;
						transition: .3s ease;
					}

				}

				&:hover > a:before{
					width: 100%;
				}
			 } 
		 }
		 > .menu-item > .sub-menu{
			 border: 1px solid #e3e3e3;
		 }
		.sub-menu{
			position: absolute;
			top: 100%;
			left: 0;
			margin: 0;
			padding: 0;
			background-color: white;
			min-width: 250px;
			box-shadow: 0 10px 30px -5px rgba(0,0,0,.1);
			pointer-events: none;
			opacity: 0;
			transform: translateY(30px);
			transition: .3s ease;
			border-bottom-left-radius: 3px;
			border-bottom-right-radius: 3px;
			

			.no-csspointerevents &{
				display: none;
				opacity: 1;
				transform: translateY(0);
			}
			.menu-item{
				display: block;
				&:not(:last-child){
					border-bottom: 1px solid #e3e3e3;
				}

				a{
					display: block;
					padding: 10px 15px;
					transition: .3s ease;
					position: relative;
					z-index: 1;
					font-size: 90%;

					&:before{
						content:" ";
						position: absolute;
						width: 100%;
						height: 100%;
						background-image: linear-gradient(90deg, $accent-color-1, $accent-color-2);
						background-color: $accent-color-1;
						color: white;
						top: 0;
						left: 0;
						z-index: -1;
						opacity: 0;
						transition: .3s ease;
					}
				}

				&:hover > a{
					color: white;
					.menu-desc{
						color: rgba(white,.7);
					}
					&:before{
						opacity: 1;
					}
				}
			}

			.sub-menu{
				top: 0;
				left: 100%;
				border-top-right-radius: 3px;
			}
		}
		.menu-desc{
			display: block;
			font-size: 70%;
			color: #999;
			line-height: 1.3;
			transition: .3s ease;
		}

		.mega-menu{
			> .sub-menu{
				display: flex;
				visibility: hidden;
				pointer-events: none;
				> .menu-item{
					width: 200px;
					display: table-cell;
					&:not(:last-child){
						border-right: 1px solid #e3e3e3;
					}
					> a{
						border-bottom: 1px solid #e3e3e3;
						.menu-label{
							font-weight: 700;
							text-transform: uppercase;
						}
						.menu-desc{
							color: inherit;
						}
					}
					&:hover > a{
						color: inherit;
						&:before{
							display: none;
						}
					}


					.sub-menu{
						opacity: 1;
						position: static;
						transform: translateY(0);
						pointer-events: auto;
						box-shadow: none;
						min-width: auto;
					}
				}
			}
		}
	 }

	//- Logo
	&--site-logo{
		padding-right : 20px;
		padding-left  : 0;
		display: flex;
		align-items: flex-start;
		justify-content: center;
		flex-direction: column;

		@media screen and (max-width: 768px){
			padding-right: 15px;
			&:after{display: none}
		}

		@media screen and (max-width: 480px){
			max-width: 150px;
		}

		.site-logo{
			margin      : 0;
			font-weight : 300;
			font-size   : 24px;
			line-height : 1;
		}

		.site-description{
			@include em(12);
			margin-top: 5px;
			display: block;
			line-height: normal;
			color: $detail-color;
		}

		a,img{ display: block }

		a{
			text-decoration  : none;
			color            : black;
			background-color : white;
			position         : relative;
			color            : $accent-color-2;

			strong{ font-weight: 500 }
			span{ color: $accent-color-1 }

		}
	}

	//- Product Search
	&--product-search{
		flex    : 1 0 auto;
		display : flex;

		form{
			width: 100%;
			display: flex;
			align-items: center;
			margin-bottom: 0;
		}

		@media screen and (max-width: 640px){ 
			display: none!important 
		}

		select{ border: none }

		.product-search-category{
			position      : relative;
			padding-right : 20px;
			max-width: 210px;

			select{ 
				background-image: none;

			}

			@media screen and (max-width: 1024px){
				display: none;
			}
			.fa{
				position: absolute;
				right : 20px;
				top   : 0;
				bottom: 0;
				margin: auto;
				height: 14px;
				pointer-events: none;
			}
		}
		.product-search-input{
			flex-grow : 1;
			position  : relative;
			overflow  : hidden;
			.line{
				content          : " ";
				width            : 100%;
				height           : 2px;
				background-color : $accent-color-1;
				background-image : linear-gradient(90deg,transparent, rgba($accent-color-2,.5) 25%, rgba($accent-color-1,.5) 50%, rgba($accent-color-2,.5) 75%, transparent);
				position         : absolute;
				bottom           : 0;
				left             : 0;
				right            : 0;
				margin           : auto;
				transform        : scaleX(0);
				transition       : .2s cubic-bezier(1,-0.02, 0.15, 1.03);
				opacity          : 0;
			}
			input{
				width         : 100%;
				border        : none;
				border-radius : 0;
				padding-left  : 0;
				padding-right : 40px;
				line-height   : inherit;
				font-size     : inherit;

				&:focus,&:active{

					~ .line{
						opacity   : 1;
						transform : scaleX(1);
						animation : line-animation 3s infinite;
					}

				}
				&.dirty + label{
					opacity        : 0;
					pointer-events : none;
					transform      : translateX(50px);
				}
			}
			label{
				position       : absolute;
				top            : 10px;
				color          : $detail-color;
				transition     : .4s ease;
				pointer-events : none;
				display        : block;

				em{
					font-family : $font-secondary;
					font-weight : 400;
				}
			}
		}
		.search-icon{
			position : absolute;
			top      : 10px;
			right    : 10px;;
			color    : $accent-color-2;
			border: none;
			background-color: transparent;
			font-size: inherit;

			.rtl &{
				left: 10px;
				right: auto;
			}
		}

		&.search-dropdown{
			flex: 0;
			
			.search-dropdown-toggle{
				border: none;
				background-color: transparent;
				font-size: 18px;
			}
			form{
				position: absolute;
				top: 100%;
				background-color: white;
				right: 0;
				width: 300px;
				padding: 15px;
				box-shadow: 0 10px 30px -3px rgba(0,0,0,.2);
				visibility: hidden;
				pointer-events: none;
				transform: translateX(30px);
				transition: .3s ease;
				opacity: 0;
				border: 1px solid #e3e3e3;

				&:before,&:after{
					content:" ";
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 10px;
					position: absolute;
					bottom: 100%;
					right: 30px;
				}
				&:before{
					border-color: transparent transparent white;
					z-index: 1;
				}
				&:after{
					border-width: 12px;
					right: 28px;
					border-color: transparent transparent #e3e3e3;
				}
				input{
					border: 1px solid #e3e3e3;
					padding: 5px;
				}
				label, .product-search-category, .line{ display: none }
				.search-icon{
					top: 10px;
					right: 10px;
				}
			}
			&:hover form{
				transform: translateX(0);
				opacity: 1;
				visibility: visible;
				pointer-events: auto;
			}
		}

	}

	&--menu-user{
		display     : flex;
		align-items : center;

		@media screen and (max-width: 1024px){ margin-left: auto; }

		@media screen and (max-width: 990px){
			display: none!important;
		}

		.menu-user-avatar{
			border           : none;
			background-color : transparent;
			padding          : 0;
			img{
				border-radius: 50%;
				width: 30px;
				height: 30px;
			}
			span{
				display: none;
			}
		}

		&:hover .menu-user-wrap{
			opacity        : 1;
			transform      : translateY(0);
			pointer-events : auto;
			z-index        : 1;
		}

		.menu-user-wrap{
			position         : absolute;
			top              : 100%;
			right            : 0;
			list-style       : none;
			padding-left     : 0;
			border           : 1px solid #e3e3e3;
			padding          : 10px 0;
			border-radius    : 3px;
			background-color : white;
			margin           : 0;
			min-width        : 200px;
			transform        : translateY(30px);
			pointer-events   : none;
			opacity          : 0;
			z-index          : -1;
			transition       : .3s ease;

			&:before{
				content      :" ";
				width        : 0;
				border-style : solid;
				border-width : 10px;
				border-color : transparent transparent white;
				position     : absolute;
				bottom       : 100%;
				right        : 35px;
				filter: drop-shadow(0 -1px 0 #e3e3e3);
			}

			.menu{
				list-style: none;
				padding: 0;
				margin: 0;

				&-item{
					white-space: nowrap;

					.fa{
						display: inline-block;
						margin-right: 10px;
						vertical-align: middle;
					}

					a{
						padding: 10px 20px;
						text-decoration: none;
						display: block;
						color: inherit;
						position: relative;
						z-index: 1;
						transition: .3s ease;

						&:after{
							content:" ";
							width: 100%;
							height: 100%;
							position: absolute;
							left: 0;
							top: 0;
							z-index: -1;
							background-image: linear-gradient(90deg, $accent-color-2, $accent-color-1);
							opacity: 0;
							transition: .3s ease;
						}
					}

					&:hover > a{
						color: white;
						&:after{
							opacity: 1;
						}
					}
				}

			}

		}
	}

	&--menu-cart{
		@media screen and (max-width: 1024px){
			padding: 0 20px;
		}
		@media screen and (max-width: 768px){
			margin-left  : auto;
			padding      : 0;
			padding-left : 15px;
		}
		@media screen and (max-width: 480px){
			position: static;
		}
		.menu-cart-trigger{
			border           : none;
			background-color : transparent;
			font-size        : inherit;
			color            : $accent-color-2;

			.cart-count{
				font-size: 11px;
				position: relative;
				top: -5px;
				font-weight: 700;
				margin-left: -5px;
				background-color: $accent-color-1;
				color: white!important;
				border-radius: 10px;
				padding: 2px 5px;
			}
		}

		&:hover .widget_shopping_cart{
			opacity: 1!important;
			pointer-events: auto;
			transform: translateY(0);
			visibility       : visible;
		}

		.widget_shopping_cart{
			position         : absolute;
			top              : 100%;
			right            : 0;
			background-color : white;
			width            : 400px;
			padding          : 50px;
			box-shadow       : 0 10px 20px -5px rgba(0,0,0,.2);
			transform        : translateY(40px);
			transition       : .3s ease;
			opacity          : 0!important;
			pointer-events   : none;
			visibility       : hidden;
			border: 1px solid #e3e3e3;

			&:before,&:after{
				content:" ";
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 10px;
				position: absolute;
				bottom: 100%;
				right: 30px;
			}
			&:before{
				border-color: transparent transparent white;
				z-index: 1;
			}
			&:after{
				border-width: 12px;
				right: 28px;
				border-color: transparent transparent #e3e3e3;
			}

			@media screen and (max-width:480px){
				padding: 30px;
				left: 0;
				width: auto
			}
		}
	}

}

@keyframes line-animation{
	0%{
		opacity: 0;
		transform: scaleX(0);
	}
	50%{
		opacity: 1;
		transform: scaleX(1.5);
	}
	100%{
		opacity: 0;
		transform: scaleX(1.5);
	}
}

.menu-main-overlay{
	background-color: rgba(0,0,0,.5);
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 98;
	pointer-events: none;
	opacity: 0;
	transition: .3s ease;
	top: 0;
	left: 0;

	@media screen and (max-width: 990px){
		display: none!important;
	}

	&.is-active{
		opacity: 1;
		pointer-events: auto;
	}
}

.user-auth-box{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 999;
	overflow: auto;
	display: none;

	.user-auth-overlay{
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,.6);
		left: 0;
		top: 0;
	}

	.user-auth-box-content{
		background-color : white;
		padding          : 25px 20px 20px;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		z-index          : 1;
		min-width        : 480px;
		left: 20px;
		right: 20px;
		margin           : 80px auto;
		max-width: 860px;

		@media screen and (max-width:640px){
			max-width: 100%;
			min-width: initial;
			top: 30px;
			transform: translateY(0);
			margin-top: 0;

			.grid-item{
				padding: 0;
				width: 100%;
				flex-basis: 100%;
				
				&.register{
					border-left: none;
					padding-top: 15px;
					border-top: 1px dashed #e3e3e3;
					margin-top: 15px;
				}
			}
		}

		.tokoo-popup__close{
			position: absolute;
			width: 36px;
			height: 36px;
			border-radius: 50%;
			right: 10px;
			top: 10px;
			border: 3px solid $accent-color-1;
			background-color: white;
			z-index: 9;
			@include em(24);
			box-shadow: none;
			line-height: 36px;
			padding: 0;

			@media screen and (max-width:640px){
				@include em(18);
				width: 32px;
				height: 32px;
				right: 15px;
				top: 15px;
				line-height: 32px;
				padding: 0;
			}
		}

		&:before{
			content:" ";
			width: 100%;
			left: 0;
			height: 5px;
			background: linear-gradient(90deg, $accent-color-1, $accent-color-2, $accent-color-1);
			position: absolute;
			left: 0;
			top: 0;
		}

		.login,.register{
			box-shadow: none;
			margin-bottom: 0;
		}

		.grid-item{
			flex-grow: 1;
			// min-width: 420px;
		}
		.section-header{
			margin-top: 0;
		}
		
		.form-row{
			margin-bottom: 10px;
			label{
				display: block;
				font-weight: 700;
				margin-bottom: 5px;
				@include em(14);
			}
		}
		label.radio{
			input{
				width: auto;
			}
		}
		.login-action{
			@include clearfix;
			> label{ 
				margin-top: 5px;
				float: left;
			}
			input[type="submit"]{
				float: right;
				padding: 5px 40px;
				margin-bottom: 30px;
			}
		}
		.register{
			border-left: 1px dashed #e3e3e3;
		}
		.lostpassword{
			text-align: right;
			display: block;
			@include em(12);
		}

		.register-action{
			text-align: right;

			input[type="submit"]{
				padding: 5px 40px;
			}
		}

		.woocommerce-password-strength{
			@include em(14);
			margin: 10px 0;
		}
		.woocommerce-password-hint{
			padding: 10px;
			border-radius: 3px;
			border: 1px solid #e3e3e3;
			display: block;
		}

	}

}