.post-grid,.post-masonry{
	.post{
		&__inner{
			background-color: white;
			box-shadow: 0 5px 10px rgba(0,0,0,.05);
			transition: .3s ease;
			position: relative;
			overflow: hidden;
			width: 100%;

			&:after{
				content:" ";
				width: 100%;
				height: 5px;
				background-image: linear-gradient(90deg,transparent, $accent-color-1, $accent-color-2, $accent-color-1, transparent);
				position: absolute;
				bottom: 0;
				left: 0;
				transform: scaleX(0);
				transition: .7s ease;
				opacity: 0;
			}
		}
		&:hover{
			.post__inner{
				box-shadow: 0 10px 20px rgba(0,0,0,.1);
				&:after{
					transform: scaleX(1.2);
					opacity: 1;
				}
			}
		}
		&__image{
			a,img{display: block}

			img{
				width: 100%;
				height: auto;
			}

		}
		&__detail{
			padding: 20px;

			@media screen and (max-width: 480px){
				padding: 15px;
			}
		}
		&__title{
			margin: 0 0 10px;
			line-height: 1.4;

			@media screen and (max-width: 480px){
				@include em(16);
			}
		}
		&__meta{
			margin-bottom: 20px;
			@include em(14);
			@media screen and (max-width: 480px){
				@include em(10);
				line-height: 1.3;
			}

			span{
				display: inline-block;
				margin-right: 10px;
				color: $detail-color;
				a:hover{
					color: $accent-color-1;
				}
			}
		}
		&__more{
			display: inline-block;
			margin-top: 20px;
			color: $accent-color-1;
			font-weight: 700;
		}
	}
}

.post-grid .post{
	display: flex;
	.post__image{
		max-height: 300px;
		overflow: hidden;

		img{
			object-fit: cover;
			max-height: 300px;
		}
	}
}

.post-list{
	.post{
		margin-bottom: 40px;
		background-color: white;
		padding: 30px 30px 35px;
		box-shadow: 0 5px 10px rgba(0,0,0,.05);
		transition: .3s ease;
		position: relative;
		overflow: hidden;
		@media screen and (max-width: 640px){
			padding: 15px;
		}

		&:after{
			content:" ";
			width: 100%;
			height: 5px;
			background-image: linear-gradient(90deg,transparent, $accent-color-1, $accent-color-2, $accent-color-1, transparent);
			position: absolute;
			bottom: 0;
			left: 0;
			transform: scaleX(0);
			transition: .7s ease;
			opacity: 0;
		}
		&:hover{
			box-shadow: 0 10px 20px rgba(0,0,0,.1);
			&:after{
				transform: scaleX(1.2);
				opacity: 1;
				// animation: line-animation 2s infinite;
			}
		}
		&__inner{
			display: table;
			width: 100%;
		}
		&__image, &__detail{
			display: table-cell;
			vertical-align: top;
			@media screen and (max-width: 640px){
				display: block;
			}
		}
		&__image{
			width: 200px;
			@media screen and (max-width: 640px){
				width: 100%;
				text-align: center;

				img{
					width: 100%;
				}
			}
		}
		&.has-post-thumbnail{
			.post__detail{
				padding-left: 30px;
				@media screen and (max-width: 640px){
					padding-left: 0;
				}
			}
		}
		&__title{
			font-family: $font-secondary;
			margin: 0 0 10px;
			line-height: 1.3;
			@include em(32);
		}
		&__meta{
			color: $detail-color;
			margin-bottom: 30px;
			position: relative;
			padding-bottom: 15px;
			&:after{
				content:" ";
				width: 50px;
				height: 3px;
				background-color: $accent-color-1;
				background-image: linear-gradient(to right, $accent-color-1, $accent-color-2);
				position: absolute;
				bottom: 0;
				left: 0;
			}
			span{
				margin-right: 10px;
			}
			a:hover{ color: $accent-color-1 }
		}
		&__more{
			margin-top: 20px;
			display: block;
			font-weight: 700;
		}
	}
}

.gallery-slider{
	position: relative;
	.slides{
		list-style: none;
		padding-left: 0;
		margin: 0;
		white-space: nowrap;
		overflow: hidden;

		li{
			display: inline-block;
			white-space: normal
		}
	}
	.flex-viewport{
		img{
			width: 100%;
			max-width: 100%;
			height: auto;
			display: block;
		}
	}

	.flex-direction-nav{
		list-style: none;
		margin: 0;
		padding-left: 0;

		.flex-next,.flex-prev{
			position: absolute;
			width: 40px;
			height: 50px;
			text-align: center;
			line-height: 50px;
			@include em(24);
			color: white;
			background-color: rgba(0,0,0,.5);
			top: 0;
			bottom: 0;
			margin: auto;
			z-index: 8;
		}
		.flex-prev{ left : 0 }
		.flex-next{ right : 0 }
		.flex-disabled{
			pointer-events: none;
			opacity: 0;
		}
	}
}

.posts-navigation{
	padding: 50px 0;
	@include clearfix;

	.next-link, .prev-link{
		padding: 5px 40px;
	}

	.next-link{ float: right }
	.prev-link{ float: left }
}

.single-post{

	.post{
		background-color : white;
		padding          : 30px 30px 70px;
		box-shadow       : 0 10px 20px -5px rgba(0,0,0,.1);
		position         : relative;
		margin-bottom    : 100px;
		
		@media screen and (max-width: 480px){
			margin-bottom: 30px;
			padding-bottom: 30px;
		}

		.gallery-slider{
			margin: -30px -30px 30px;
		}

		&__header{
			position: relative;
			padding: 30px 90px 0;

			@media screen and (max-width: 990px){
				padding: 0;
			}

		}

		&.has-post-thumbnail .post__header{
			padding : 300px 90px 70px;
			z-index : 1;
			margin  : -30px -30px 50px;

			@media screen and (max-width: 768px){
				padding: 100px 20px 20px;
				margin-bottom: 30px;
			}

			.post__image,.post__image .bg{
				position : absolute;
				width    : 100%;
				height   : 100%;
				z-index  : -1;
				top      : 0;
				left     : 0;
			}
			.post__image{
				overflow: hidden;
				.bg{
					background-size : cover;
					background-position: center;
					height          : 200%;
					top             : -100%
				}
				&:after{
					content    :" ";
					background : linear-gradient(transparent, rgba(0,0,0,.7));
					position   : absolute;
					width      : 100%;
					height     : 100%;
					left       : 0;
					top        : 0;
				}
			}
			.post__title{
				color: white;
			}

			.post__meta{
				margin-bottom: 0;
			}
			.post__date, .post__meta{
				color: white;
			}
			.post__meta:before{
				background-color: white;
			}
		}

		&__title{
			font-size: 3em;
			font-family: $font-secondary;
			line-height: 1.3;
			margin: 0 0 20px;

			@media screen and (max-width: 480px){
				@include em(32);
			}
		}
		&__meta{
			padding-left : 70px;
			position     : relative;
			margin-bottom: 50px;
			@media screen and (max-width: 480px){
				padding-left: 0;
				@include em(12);
			}

			&:before{
				content          : " ";
				width            : 60px;
				height           : 1px;
				position         : absolute;
				background-color : $body-color;
				top              : 0;
				bottom           : 4px;
				left             : 0;
				margin           : auto;
				@media screen and (max-width: 480px){
					display: none;
				}
			}
			span{
				display: inline-block;
				margin-right: 10px;
			}

			a{ color: $accent-color-2; }
		}

		&__content{
			padding: 30px 70px;

			@media screen and (max-width: 990px){
				padding: 0;
			}
		}

		&__footer{
			padding: 30px 70px;
			@include clearfix;
			@media screen and (max-width: 640px){
				padding: 0;
			}
		}

		&__tags{
			padding-left: 120px;
			position: relative;
			strong{
				color: $detail-color;
				font-weight: normal;
				position: absolute;
				left: 0;
				text-align: right;
				padding-left: 15px;

				&:before{
					content:" ";
					width: 50px;
					height: 1px;
					background-color: $border-color;
					margin-right: 10px;
					display: inline-block;
					vertical-align: middle;
				}
			}
			a{ color: $heading-color }
		}
		&__share{
			width           : 100%;
			position        : absolute;
			left            : 0;
			top             : 100%;
			display         : flex;
			align-items     : flex-start;
			transform-style : preserve-3d;
			perspective     : 1000px;
			@media screen and (max-width: 480px){
				display: block;
				top: auto;
				bottom: 0;
				position: relative;
			}

			strong, a{
				flex: 1;
				padding: 15px 10px;
				text-align: center;
				background-color: white;
				transform-origin: 50% 0;
				box-shadow: 0 10px 15px -5px rgba(0,0,0,.1);

				&.animated{
					animation: shakeShare 2s ease;
					box-shadow: 0 10px 15px -5px rgba(0,0,0,.3);
				}
			}
			strong{
				font-size: 18px;
				padding: 20px 10px;
				@media screen and (max-width: 480px){
					display: block;
				}
			}
			a{
				color: white;
				font-size: 24px;
				@media screen and (max-width: 480px){
					width: 50%;
					float: left;
					font-size: 16px;
					padding: 10px;
				}
			}
			.label{
				display: none;
			}

			.facebook{ background-color: $facebook;}
			.twitter{ background-color: $twitter;}
			.google-plus{ background-color: $google-plus;}
			.linkedin{ background-color: $linkedin;}
		}

		&__nav{
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			padding: 0 0 50px;

			&--prev, &--next{
				flex: 1 0 50%;
				padding: 30px;
				margin: 30px 0;
				position: relative;
				@media screen and (max-width: 480px){
					margin: 15px 0;
				}

				&:before{
					font-family: "dripicons-v2";
					font-size: 18px;
					position: absolute;
					top: 30px;
				}

				@media screen and (max-width: 480px){
					flex-basis: 100%;
					text-align: left!important;
				}
			}
			&--prev{
				text-align: left;
				&:before{
					content:"\55";
					left: 0;
				}
				@media screen and (min-width:481px){
					border-right: 1px solid #e3e3e3;
				}
			}
			&--next{
				text-align: right;
				&:before{
					content:"\56";
					right: 0;
					@media screen and (max-width:480px){
						right: auto;
						left: 0;
					}
				}
			}

			h2{
				margin-top: 0;
				margin-bottom: 5px;
				line-height: 1.3;
			}
		}
		&__comment{
			background-color: white;
			padding: 30px;
			box-shadow: 0 10px 20px -5px rgba(0,0,0,.2);
			margin-bottom: 100px;
		}
	}
}

.comment{
	&-list,&list{
		list-style: none;
		padding: 0;
		margin: 0 0 100px;
	}

	&s-area{
		.section-header{
			margin-top: 0;
		}
	}

	&-body{
		padding-left: 80px;
		position: relative;
		margin-bottom: 30px;
		padding-bottom: 30px;
		border-bottom: 1px dashed #e0e0e0;

		@media screen and (max-width: 640px){
			padding-left: 0;
		}
	}
	&-meta{
		@media screen and (max-width: 640px){
			padding-left: 80px;
			position: relative;
			min-height: 50px;
			margin-bottom: 30px;
		}
		.avatar{
			position: absolute;
			left: 0;
			top: 0;
			border-radius: 50%;
		}
		.fn{
			color: $heading-color;
			font-family: $font-secondary;
			font-style: normal;
			@include em(18);

			a{
				color: inherit;
			}
		}
		&data{
			color: $detail-color;
			font-size: 14px;
		}
	}

	&-content{
		margin-bottom: 30px;
	}

	.reply{
		a{
			@extend .button--special;
			padding-right: 20px;

			&:after{
				background-color: $accent-color-1;
			}
		}
		span{ display: none }
	}

	.children{
		margin-left  : 80px;
		list-style   : none;
		padding-left : 0;

		@media screen and (max-width: 640px){ margin-left: 0; }

	}
	&-respond{
		.section-header{
			margin-top: 0;
		}
		.comment-notes,.form-submit,.logged-in-as{
			padding: 0 15px;
			clear: both;
		}
		.form-submit{
			text-align: right;
			padding-top: 15px;
			display: block;

			input{
				@extend .button;
			}
		}
		.comment{
			&-form{
				margin: 0 -15px;
				&-rating,.stars{
					padding: 0 15px;
				}
				&-comment{
					width: 100%;
					padding: 0 15px;
					margin: 0 0 30px;

					textarea{
						width: 100%;
						padding: 20px 15px;
						display: block;
						border-radius: 20px;
					}
				}
				&-author, &-email, &-url{
					width: 33.3333%;
					float: left;
					padding: 0 15px;
					margin: 0 0 10px;

					@media screen and (max-width: 640px){
						width: 100%;
					}

					input{
						width: 100%;
						display: block;
					}
				}
			}
		}
	}
}

.bypostauthor{
	.avatar{
		border: 2px solid $accent-color-1;
	}
	.comment-body{
		border-bottom-color: $accent-color-1;
	}
}

@keyframes shakeShare{
	0%  { transform: rotateX(0) }
	15%  { transform: rotateX(40deg) }
	30% { transform: rotateX(-30deg) }
	50% { transform: rotateX(20deg) }
	60% { transform: rotateX(-15deg) }
	75% { transform: rotateX(10deg) }
	100%{ transform: rotateX(0) }
}

.entry-content,
.page-content,
.comment-content{
	
	h1,h2,h3,h4,h5,h6{
		margin-top: 50px;
		margin-bottom: 15px;

	}
	h1{ @include em(36) }
	h2{ @include em(32) }
	h3{ @include em(28) }
	h4{ @include em(24) }
	h5{ @include em(18) }
	h6{ @include em(16) }

	.leading{
		@include em(21);
		font-family: $font-secondary;
		line-height: 1.7;
	}

	blockquote{
		font-family  : $font-secondary;
		@include em(24);
		font-style   : italic;
		color        : $heading-color;
		border-left  : 1px solid $border-color;
		margin-left  : 0;
		padding-left : 50px;

		p{
			font-family: inherit;
			font-size: inherit;
			font-style: inherit;
			color: inherit;
			line-height: inherit;
		}

		cite{
			display: block;
			padding: 40px 0 0;
			font-family: $root-fontFamily;
			font-style: normal;
			@include em(14, 24);

			&:before{
				content:" ";
				width: 40px;
				height: 1px;
				background-color: $border-color;
				display: inline-block;
				vertical-align: middle;
				margin-right: 10px;
			}
		}
	}
	img{
		height: auto;
	}
	.alignleft{
		margin: 30px 30px 30px 0;
		max-width: 60%;
	}
	.alignright{
		margin: 30px 0 30px 30px;
		max-width: 60%;
	}

	ul, ol{
		margin: 15px 0;
		padding-left: 20px;
		li{
			// margin: 10px 0;
		}
	}
}

.alignleft{ float: left }
.alignright{ float: right }
.wp-caption{
	margin: 30px 0;
	img{ height: auto }
	&.alignleft{ margin-right: 30px }
	&.alignright{ margin-left: 30px }
	&.aligncenter{
		margin: 30px auto;
		text-align: center;
		max-width: 100%!important;
	}

	&-text{
		color: $detail-color;
	}
}
.gallery-caption{
	color: $detail-color;
	font-style: italic;
	@include em(14);
}

.comments-nav{
	position: relative;
	padding: 30px 0;
	margin-bottom: 30px;
	z-index: 1;

	.page-numbers{
		position: absolute;
		text-align: center;
		z-index: -1;
		width: 100%;
		left: 0;
		@media screen and (max-width: 768px){
			display: none;
		}
	}
	.comment-navigation{
		@include clearfix;

		.nav-previous,.nav-next{
			a{
				border: 2px solid $accent-color-1;
				border-radius: 40px;
				padding: 5px 30px;
				display: block;
			}
		}
		.nav-previous{
			float: left;
		}
		.nav-next{
			float: right;
		}
	}
}