*{
	box-sizing: border-box;
	&:before, &:after{
		box-sizing: border-box;
	}
}
:root{ font-size: #{$root-fontSize}px }

body{
	font-family: $root-fontFamily;
	font-weight: $root-fontWeight;
	line-height: $root-lineHeight;
	margin: 0;
	background-color: #f6f6f6;
	color: $body-color;
	word-break: break-word;
}

h1,h2,h3,h4,h5,h6{
	color: $heading-color;
	line-height: normal;
	font-family: $font-secondary;
	font-weight: 700;
}

a{
	text-decoration: none;
	color: inherit;
	transition: .3s ease;
}

figure, img, iframe{
	max-width: 100%;
}

figure{
	margin: 0;
}

.screen-reader-text{
	position: absolute;
	width: 1px;
	height: 1px;
	visibility: hidden;
	overflow: hidden;


}

.site-content{
	@include clearfix;
	overflow-x: hidden;
}

.main-content{
	min-height: 50vh;
}