//- Section Title Component
.section-header {
	position       : relative;
	padding-bottom : 20px;
	margin         : 30px 0;

	&:after{
		content          :" ";
		width            : 30px;
		height           : 3px;
		background-color : $accent-color-2;
		position         : absolute;
		left             : 0;
		bottom           : 0;
	}
	
	.section-title {
		margin      : 0;
	}
	.section-subtitle {
		@include em(15);
	}
	&--left{
		text-align: left;
		&:after{
			left  : 0;
			margin : 0 auto;
		}
	}
	&--center{
		text-align: center;
		&:after{
			right  : 0;
			margin : 0 auto;
		}
	}
	&--right{
		text-align: right;
		&:after{
			left  : auto;
			right : 0;
		}
		
	}
}

//- Category Icon Component
.category-icon{
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	padding: 30px 0;
	
	.grid-item{
		padding: 0 15px;

		&.category-icon__title{
			padding: 10px 15px 10px 15px;
		}
		&.category-icon__cat{
			padding: 15px 15px 15px 80px;
			// @media screen and (max-width: 480px){
			// 	padding-left: 60px;
			// }
		}
		&:hover{
			.category-icon__cat-image{
				color: white;

				&:after{
					transform: scale(1);
				}
			}
		}
	}
	&__title, &__cat{
		flex: 1 0 auto;
		@media screen and (max-width:  1200px){ width: 33.3333%;}
		@media screen and (max-width:  768px){ width: 50%;}
		@media screen and (max-width:  480px){ width: 100%;}
	}
	&__title{
		font-family: $font-secondary;
		position: relative;
		margin-bottom: 30px;
		@include em(21);
		padding-bottom: 5px;
		&:after{
			content:" ";
			width: 20px;
			height: 3px;
			background-color: $accent-color-2;
			position: absolute;
			bottom: 0;
			left:15px;
		}
	}
	&__cat{
		padding-left: 75px;
		position: relative;

		&-image{
			position: absolute;
			left: 15px;
			top: 15px;
			width: 60px;
			height: 60px;
			z-index: 1;
			text-align: center;
			display: flex;
			align-items: center;
			justify-content: center;
			@include em(40);
			transition: .3s ease;
			@media screen and (max-width: 480px){
				@include em(32);
			}

			.no-flex &{
				padding-top: 15%;
				left: 0;
			}

			&:after{
				content:" ";
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 0;
				z-index: -1;
				border-radius: 50%;
				background-color: $accent-color-1;
				background-image: linear-gradient(45deg, $accent-color-1, $accent-color-2);
				transition: transform .3s ease;
				transform: scale(0);
			}

			img{
				max-width: 48px;
				display: block;
			}
		}
		&-title{
			margin: 5px 0 0;
			color: $accent-color-2;
			line-height: 1;
			@include em(21);
		}
		&-count{
			color: $detail-color;
			@include em(15);
		}
	}
}

// Tokoo Separator
.tokoo-separator{
	border-style: solid;
	border-top: none;
	color: $border-color;

	&--dotted{ 
		border-style: dotted;
		border-top: none;
		border-width: 2px;
	}

	&--dashed{ 
		border-style: dashed;
		border-top: none;
	}
}


.featured-author{
	text-align: center;
	margin-bottom: 30px;

	&__image{
		border-radius : 50%;
		width         : 100px;
		height        : 100px;
		overflow      : hidden;
		margin        : 0 auto 20px;
		transition: .3s ease;

		img{
			width: 100px;
			height: 100px;
			object-fit: cover;
			display: block;
		}
	}
	&:hover{
		.featured-author__image{
			box-shadow: 0 0 0 10px rgba(white,.9);
		}
	}
	&__name{
		font-weight: 600;
		margin: 0;
		line-height: 1;
		@include em(18);
	}
	&__books{
		color: $detail-color;
		font-family: font-secondary;
		font-size: 15px;
		font-style: italic;
	}
	
}


.popular-series{
	.series{
		text-align: center;
	}
}

.tokoo-map{
	min-height: 400px;
}

.tokoo-contact-form{
	.row{
		margin-bottom: 30px;
	}
	input:not([type="submit"]),textarea{
		width: 100%;
		max-width: 100%;
	}
	textarea{
		height: 120px;
		width: 100%;
	}
}

.pstk-custom-block {
	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 960px){
		padding-top: 55px;
		padding-bottom: 55px;
	}

	@media screen and (max-width: 480px){
		padding-top: 35px;
		padding-bottom: 35px;
	}

	@media screen and (min-width: 961px){
		.is-table-row {
			display: table;
			width: 100%;
		}

		.is-table-row [class*="col-"] {
	        float: none;
	        display: table-cell;
	        vertical-align: middle;
	    }
	}

	.section-title {
		font-size: 26px;
	    font-weight: 300;
	    text-transform: uppercase;
	}

	.book-title {
		font-family: Playfair Display;
	    font-size: 48px;
	    font-weight: 300;
	    margin-top: 0px;
	    margin-bottom: 16px;
	}

	.book-price {
	    color: #c017e0;
	    font-family: Open Sans;
	    font-size: 32px;
	    font-weight: 300;
	}

	.detail-img {
		text-align: center;

		img {
			box-shadow: -1px 16px 111px -19px rgba(153,153,153,1);
		}

		@media screen and (max-width: 960px){
			margin-top: 50px;
		}
	}

	&.detail-product-1{
		.detail-icon {
			font-size: 58px;
			color: #c017e0;
		}

		h2 {
			color: #363636;
		    font-family: Playfair Display;
		    font-size: 45px;
		    line-height: 48px;
		    font-weight: 300;
		}

		p {
			margin-bottom: 50px;
		}

		.detail-img {
			text-align: center;
			
			img {
				box-shadow: none;
			}
			@media screen and (max-width: 960px){
				margin-top: 50px;
			}
		}
	}

	&.detail-product-2 {

		.left-col {
			text-align: right;
		}

		.right-col {
			text-align: left;
		}
	}

	&.detail-product-3 {

		.left-col {
			text-align: right;
		}

		.center-col {
			text-align: center;
		}

		.right-col {
			text-align: left;
		}
	}

	&.detail-product-4 {
		background: linear-gradient(rgba(61, 61, 61, 0.43), rgba(61, 61, 61, 0.43)), transparent url(http://theme.dev/pustaka/wp-content/themes/pustaka/assets/img/dummy/blog_13.jpg) center center/cover no-repeat scroll;
    	padding-bottom: 0px;

		.left-col {
			text-align: right;
			padding-top: 200px;
			padding-bottom: 50px;

			@media screen and (max-width: 960px){
				padding-top: 50px;
			}
		}

		.detail-img img {
			max-width: 500px;
			width: 100%;
			height: auto;
			box-shadow: -1px 16px 111px -19px rgba(0,0,0,1);
			margin-bottom: -120px;
		}

		.book-title, .author-name, p {
			color: white;
		}

		.button {
			color: white;
			border-color: white;
			text-transform: uppercase;

			&:hover {
				background-color: #c017e0;
				border-color: #c017e0;
			}
		}
	}

	&.detail-product-5 {
		background-color: #ffffff;

		.row {
			margin-top: -150px;
		}
		
		.detail-img img {
			max-width: 500px;
			width: 100%;
			height: auto;
		}

		.right-col {
			@media screen and (max-width: 960px){
				padding-top: 50px;
			}
		}
	}

	&.detail-product-6 {
		padding-top: 50px;
		padding-bottom: 50px;

		.left-col {
			text-align: left; 
		}

		.right-col {
			text-align: center;
			margin-top: -100px;

			@media screen and (max-width: 960px){
				margin-top: initial;
			}
		}
	}  
}