.widget{

	&.widget_shopping_cart{
		li{ padding-right: 40px }
		.quantity{
			display: block;
			color: $accent-color-1;

			.amount{display: inline-block;}
		}
		.empty{
			text-align: center;
			padding-right: 0;
			margin-bottom: 0;
			display: block;

			&:before{
				content:"\e042";
				font-family: "dripicons-v2";
				@include em(64);
				line-height: 1;
				margin-bottom: 30px;
				display: block;
				color: #e3e3e3;
			}
		}
		.remove{
			position: absolute;
			right: 0;
			top: 5px;
			width: 14px;
			height: 14px;
			font-size: 14px;
			overflow: hidden;
			text-indent: -9999px;
			color: $detail-color;
			transition: .3s ease;

			&:hover{
				color: darken(red,20%);
			}

			&:before{
				content:"\e053";
				font-family: "dripicons-v2";
				position: absolute;
				left: 0;
				top: 0;
				line-height: 1;
				text-indent: 0;
			}
		}
		.total{
			border-top      : 1px solid $border-color;
			border-bottom   : 1px solid $border-color;
			padding         : 20px 0;
			line-height     : 1;
			display         : flex;
			justify-content : space-between;
			font-weight     : 700;

			.amount{
				color: $accent-color-1;
			}
		}
		.buttons{
			a{
				display: block;
				margin-bottom: 10px;
				text-align: center;

				&.checkout{
					color: $heading-color;
					border-color: inherit;
				}
			}
		}
	}

	&.widget_rating_filter{
		ul{
			text-align: right;
			li{
				@include clearfix;
			}
			.star-rating{
				float: left;
				text-align: left;
			}
		}
	}
	&.widget_layered_nav{
		li{
			@include clearfix;
		}
		.count{
			float: right;
		}
	}

	&.widget_price_filter {
		.price_slider.ui-slider{
			margin-bottom: 20px;
			width: 100%;
			height: 5px;
			background-color: #f1f4f6;
			background-color: white;
			position: relative;
			box-shadow: 0 2px 5px rgba(0,0,0,.1);

			.ui-slider-range{
				position: absolute;
				height: 100%;
				top: 0;
				background-color: $accent-color-2;
			}
			.ui-slider-handle{
				width: 15px;
				height: 15px;
				position: absolute;
				top: -5px;
				background-color: white;
				box-shadow: 0 2px 10px rgba(#c0ddee,.7);
				cursor: pointer;
			}
		}
		.price_slider_amount{
			@include clearfix;
			.button{
				padding: 5px 20px;
			}
			.price_label{
				float: right;
				padding-top: 10px;

				span{
					color: $accent-color-1;
					font-weight: 700;
				}
			}
			.clear{display: none}
		}
	}

	&.widget_product_search{
		.woocommerce-product-search{
			position: relative;
			@include clearfix;
		}
		.search-field{
			width: 100%;
			appearance: none;
			margin-bottom: 10px;
		}
		input[type="search"],
		input[type="text"]{
			width: 100%;
		}
		input[type="submit"]{
			position: absolute;
			right: 0;
			background-color: $accent-color-1;
			color: white;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
	}

	&.widget_product_categories{
		select{
			width: 100%;
		}
	}

	&.woocommerce-best-selling-product{
		.product_list_widget{
			counter-reset: best;
			li{
				counter-increment: best;
				position: relative;
				&:before{
					content          : "#" counter(best);
					width            : 48px;
					height           : 48px;
					font-size        : 20px;
					color            : white;
					background-color : #e2a71c;
					border-radius    : 50%;
					position         : absolute;
					text-align       : center;
					padding-top      : 2px;
					left             : -20px;
					top              : 10px;
					border           : 4px solid #bd8e1f;
				}
			}
		}
	}

	&.woocommerce-top-product{
		.product_list_widget{
			counter-reset: top;
			li{
				counter-increment: top;
				position: relative;
				padding-left: 20px;

				&:before{
					content: counter(top) ".";
					position: absolute;
					display: inline-block;
					top: 0;
					left: 0;
					font-weight: 700;
					font-style: italic;
				}
			}
			
			&.has_image{
				counter-reset: topbook;
				li{
					padding-left: 0;
					counter-increment: topbook;
					&:before{
						content          : "#" counter(topbook);
						width            : 48px;
						height           : 48px;
						font-size        : 20px;
						color            : white;
						background-color : #e2a71c;
						border-radius    : 50%;
						position         : absolute;
						text-align       : center;
						padding-top      : 4px;
						left             : -20px;
						top              : 10px;
						border           : 4px solid #bd8e1f;
					}
				}
			}

		}
	}

	&.widget_recent_reviews{
		.product_list_widget{
			li{
				padding-left: 70px;
				position: relative;
				min-height: 70px;
				display: block;
			}
			img{
				position: absolute;
				left: 0;
				top: 0;
				width: 60px;
				height: auto;
				box-shadow: 2px 3px 9px rgba(0,0,0,.2);
			}
			a{
				display: block!important;
			}
		}
	}
	.product_list_widget li{
		padding-top: 10px;
		padding-bottom: 10px;

	}
	&.widget_recent_reviews .product_list_widget {
		li{
			padding-top: 15px;
			padding-bottom: 15px;
			min-height: 90px;
		}
		img{
			top: 15px;
		}
	}
}

.product_list_widget{
	list-style: none;
	padding-left: 0;

	li{
		// margin-bottom: 20px;
		position: relative;
		display: flex;
		padding-top: 10px;
		padding-bottom: 10px;
		@include clearfix;
	}
	.product-image{
		width: 60px;
		margin-right: 10px;
		flex-shrink: 0;

		img{
			box-shadow: 2px 3px 9px rgba(0,0,0,.2);
		}
	}
	.product-title{
		display: block;
		font-family: $font-secondary;
		color: $heading-color;
	}
	.product-detail{
		> a{
			font-weight: 700;
				display: block;
				font-family: $font-secondary;
				color: $heading-color;
		}
	}
	.span-author{
		color       : $detail-color;
		font-family : $font-secondary;
		font-style  : italic;
	}
	del,ins{
		display: inline-block;
	}
	.woocommerce-Price-amount amount{
		@include em(14px);
	}
	img{
		width: 100%;
		height: auto;
	}

	&.cart_list{
		li{
			display: flex;

			.product-image{
				@include clearfix;
			}

			img{
				float: left;
				margin-right: 10px;
				width: 60px;
				height: auto;
			}
		}
	}

}
.koo-image-slider{
	.slides{
		overflow: hidden;
		white-space: nowrap;

		li{
			display: inline-block;
			opacity: 0;
			transition: opacity .3s ease;
		}
	}
	.flex-viewport{
		li{
			opacity: 1;
		}
	}
}