.widget{
	padding: 0 10px;
	margin-bottom: 30px;
	&-title{
		font-family: $font-secondary;
		font-size: 1.5em;
	}

	ul, ol{
		margin: 0;
		padding: 0;
		list-style: none;

		li{
			padding-top: 5px;
			padding-bottom: 5px;
			&:not(:last-child){
				border-bottom: 1px solid rgba(0,0,0,.1);
			}
		}

		a{
			color: inherit;
			text-decoration: none;
		}
	}
}
@import "component.widgets-tokoo";
@import "component.widgets-woocommerce";
@import "component.widgets-wordpress";