

.social-links{
	a{
		display: inline-block;
		padding: 5px!important;
		line-height: 1;
	}
	&.small{
		font-size: 16px;
	}
	&.medium{
		font-size: 20px;
	}
	&.large{
		font-size: 32px;
	}
	&.boxed,&.rounded{

		&.small a{
			width: 32px;
			height: 32px;
			line-height: 1.4;
		}
		&.medium a{
			width: 48px;
			height: 48px;
			line-height: 1.8;
		}
		&.large a{
			width: 64px;
			height: 64px;
			line-height: 1.6;
		}
		a{
			color      : white;
			display    : inline-block;
			text-align : center;

			&.facebook		{
				background-color: $facebook;
				&:hover{ background-color: lighten( $facebook, 10% ) }
			}
			&.twitter		{
				background-color: $twitter;
				&:hover{ background-color: lighten( $twitter, 10% ) }
			}
			&.google-plus	{
				background-color: $google-plus;
				&:hover{ background-color: lighten( $google-plus, 10% ) }
			}
			&.linkedin		{
				background-color: $linkedin;
				&:hover{ background-color: lighten( $linkedin, 10% ) }
			}
			&.spotify		{
				background-color: $spotify;
				&:hover{ background-color: lighten( $spotify, 10% ) }
			}
			&.codepen		{
				background-color: $codepen;
				&:hover{ background-color: lighten( $codepen, 10% ) }
			}
			&.digg			{
				background-color: $digg;
				&:hover{ background-color: lighten( $digg, 10% ) }
			}
			&.foursquare	{
				background-color: $foursquare;
				&:hover{ background-color: lighten( $foursquare, 10% ) }
			}
			&.github		{
				background-color: $github;
				&:hover{ background-color: lighten( $github, 10% ) }
			}
			&.reddit		{
				background-color: $reddit;
				&:hover{ background-color: lighten( $reddit, 10% ) }
			}
			&.skype			{
				background-color: $skype;
				&:hover{ background-color: lighten( $skype, 10% ) }
			}
			&.behance		{
				background-color: $behance;
				&:hover{ background-color: lighten( $behance, 10% ) }
			}
			&.youtube		{
				background-color: $youtube;
				&:hover{ background-color: lighten( $youtube, 10% ) }
			}
			&.steam			{
				background-color: $steam;
				&:hover{ background-color: lighten( $steam, 10% ) }
			}
			&.dribbble		{
				background-color: $dribbble;
				&:hover{ background-color: lighten( $dribbble, 10% ) }
			}
			&.tumblr		{
				background-color: $tumblr;
				&:hover{ background-color: lighten( $tumblr, 10% ) }
			}
			&.wordpress		{
				background-color: $wordpress;
				&:hover{ background-color: lighten( $wordpress, 10% ) }
			}
			&.instagram		{
				background-color: $instagram;
				&:hover{ background-color: lighten( $instagram, 10% ) }
			}
			&.pinterest		{
				background-color: $pinterest;
				&:hover{ background-color: lighten( $pinterest, 10% ) }
			}
			&.flickr		{
				background-color: $flickr;
				&:hover{ background-color: lighten( $flickr, 10% ) }
			}
			&.vimeo			{
				background-color: $vimeo;
				&:hover{ background-color: lighten( $vimeo, 10% ) }
			}
			&.vine			{
				background-color: $vine;
				&:hover{ background-color: lighten( $vine, 10% ) }
			}
			&.deviantart	{
				background-color: $deviantart;
				&:hover{ background-color: lighten( $deviantart, 10% ) }
			}
			&.lastfm		{
				background-color: $lastfm;
				&:hover{ background-color: lighten( $lastfm, 10% ) }
			}
			&.soundcloud	{
				background-color: $soundcloud;
				&:hover{ background-color: lighten( $soundcloud, 10% ) }
			}
		}
	}
	&.rounded{
		a{
			border-radius: 50%;
		}
	}
}